// this file contains utils used by torus-geocoder package (widget)
import versionJson from '../../../version.json';

export interface RequestParams {
  format?: string;
  withCredentials?: boolean;
  method?: 'POST' | 'GET';
  data?: any;
}

export function post(url: string, params: RequestParams) {
  return request(url, params);
}

export function request(url: string, params: RequestParams) {
  return new Promise((resolve, reject) => {
    const req = new XMLHttpRequest();
    const format: string = params.format || 'json';
    req.withCredentials = params.withCredentials || false;
    req.onload = () => {
      const onError = (e) => {
        reject(e);
      };

      if (req.status === 200) {
        try {
          let toResolve;
          if (format === 'json') {
            toResolve = JSON.parse(req.response);
            if (toResolve.d) { //  validateJSON(value)
              try {
                toResolve = JSON.parse(toResolve.d);
              } catch (er) {
                toResolve = toResolve.d; // not a json, firstShow control just a date string
              }
            }
          } else {
            toResolve = req.response.d || req.response;
          }
          resolve(toResolve);
        } catch (er) {
          onError(er);
        }
      } else {
        let text = req.statusText;
        try {
          const respObj = JSON.parse(req.response);
          if (respObj.Message) {
            text = respObj.Message;
          }
        } catch (er) {
          // ignore
        }
        onError(text);
      }
    };

    req.onerror = () => {
      const errStr = 'Network Error';
      const error = Error(errStr);
      reject(error);
    };
    try {
      const method = params.method || 'GET';
      req.open(method.toUpperCase(), url, true);
      let body: any;

      // add app version to params
      if (typeof params.data == 'undefined') {
        params.data = {};
      }
      params.data.source = 'widget_' + versionJson.number;

      body = JSON.stringify(params.data || {});
      req.setRequestHeader('Content-Type', 'application/json; charset=utf-8');
      // this._setRequestHeader(req, { charset: params.charset });

      req.send(body);
    } catch (er) {
      reject(er);
    }
  }).then((data) => {
    return data;
  }).catch((er) => {

    return Promise.reject(er);
  });
}
