import L from 'leaflet';
import '../Esri/EsriLeaflet';
import { EsriLeaflet } from '../Esri/EsriLeaflet';

const protocol = window.location.protocol === 'https:' ? 'https:' : 'http:';

export let EsriLeafletGeocoding: {
    WorldGeocodingService: string;
    Tasks: any;
    Services: any;
    Controls: any;
} = {
        WorldGeocodingService: protocol + '//geocode.arcgis.com/arcgis/rest/services/World/GeocodeServer/',
        Tasks: {},
        Services: {},
        Controls: {},
    };

if (typeof window !== 'undefined' && L && EsriLeaflet) {
    EsriLeaflet.Geocoding = EsriLeafletGeocoding;
}
