/* tslint:disable */
import L from "leaflet";

declare global {
    interface Window {
        _EsriLeafletCallbacks: any
        EsriLeaflet

    }
}

export const EsriLeaflet: any = { // jshint ignore:line
    VERSION: "1.0.0",
    Layers: {},
    Services: {},
    Controls: {},
    Tasks: {},
    Util: {},
    Request: {},
    Support: {
        // @ts-ignore
        CORS: !!(window.XMLHttpRequest && "withCredentials" in new XMLHttpRequest()),
        pointerEvents: document.documentElement.style.pointerEvents === "",
    },
};

if (window) {
    window.EsriLeaflet = EsriLeaflet;
}

(function (EsriLeaflet) {

    let callbacks = 0;

    window._EsriLeafletCallbacks = {};

    function serialize(params) {
        let data = "";

        params.f = params.f || "json";

        for (const key in params) {
            if (params.hasOwnProperty(key)) {
                const param = params[key];
                const type = Object.prototype.toString.call(param);
                let value;

                if (data.length) {
                    data += "&";
                }

                if (type === "[object Array]") {
                    value = (Object.prototype.toString.call(param[0]) === "[object Object]") ?
                        JSON.stringify(param) : param.join(",");
                } else if (type === "[object Object]") {
                    value = JSON.stringify(param);
                } else if (type === "[object Date]") {
                    value = param.valueOf();
                } else {
                    value = param;
                }

                data += encodeURIComponent(key) + "=" + encodeURIComponent(value);
            }
        }

        return data;
    }

    function createRequest(callback, context) {
        const httpRequest = new XMLHttpRequest();

        httpRequest.onerror = function () {
            httpRequest.onreadystatechange = L.Util.falseFn;

            callback.call(context, {
                error: {
                    code: 500,
                    message: "XMLHttpRequest error",
                },
            }, null);
        };

        httpRequest.onreadystatechange = function () {
            let response;
            let error;

            if (httpRequest.readyState === 4) {
                try {
                    response = JSON.parse(httpRequest.responseText);
                } catch (e) {
                    response = null;
                    error = {
                        code: 500,
                        message: "Could not parse response as JSON. This could also be caused by a CORS or XMLHttpRequest error.",
                    };
                }

                if (!error && response.error) {
                    error = response.error;
                    response = null;
                }

                httpRequest.onerror = L.Util.falseFn;

                callback.call(context, error, response);
            }
        };

        return httpRequest;
    }

    // AJAX handlers for CORS (modern browsers) or JSONP (older browsers)
    EsriLeaflet.Request = {
        request(url, params, callback, context) {
            const paramString = serialize(params);
            const httpRequest = createRequest(callback, context);
            const requestLength = (url + "?" + paramString).length;

            // request is less then 2000 characters and the browser supports CORS, make GET request with XMLHttpRequest
            if (requestLength <= 2000 && EsriLeaflet.Support.CORS) {
                httpRequest.open("GET", url + "?" + paramString);
                httpRequest.send(null);

                // request is less more then 2000 characters and the browser supports CORS, make POST request with XMLHttpRequest
            } else if (requestLength > 2000 && EsriLeaflet.Support.CORS) {
                httpRequest.open("POST", url);
                httpRequest.setRequestHeader("Content-Type", "application/x-www-form-urlencoded");
                httpRequest.send(paramString);

                // request is less more then 2000 characters and the browser does not support CORS, make a JSONP request
            } else if (requestLength <= 2000 && !EsriLeaflet.Support.CORS) {

                // eslint-disable-next-line new-cap
                return EsriLeaflet.Request.get.JSONP(url, params, callback, context);

                // request is longer then 2000 characters and the browser does not support CORS, log a warning
            } else {
                EsriLeaflet.Util.warn("a request to " + url + " was longer then 2000 characters and this browser cannot make a cross-domain post request. Please use a proxy http://esri.github.io/esri-leaflet/api-reference/request.html");
                return;
            }

            return httpRequest;
        },

        post: {
            XMLHTTP(url, params, callback, context) {
                const httpRequest = createRequest(callback, context);
                httpRequest.open("POST", url);
                httpRequest.setRequestHeader("Content-Type", "application/x-www-form-urlencoded");
                httpRequest.send(serialize(params));

                return httpRequest;
            },
        },

        get: {
            CORS(url, params, callback, context) {
                const httpRequest = createRequest(callback, context);

                httpRequest.open("GET", url + "?" + serialize(params), true);
                httpRequest.send(null);

                return httpRequest;
            },
            JSONP(url, params, callback, context) {
                const callbackId = "c" + callbacks;

                params.callback = "window._EsriLeafletCallbacks." + callbackId;

                const script = L.DomUtil.create("script", null, document.body) as HTMLScriptElement;
                script.type = "text/javascript";
                script.src = url + "?" + serialize(params);
                script.id = callbackId;

                window._EsriLeafletCallbacks[callbackId] = function (response) {
                    if (window._EsriLeafletCallbacks[callbackId] !== true) {
                        let error;
                        const responseType = Object.prototype.toString.call(response);

                        if (!(responseType === "[object Object]" || responseType === "[object Array]")) {
                            error = {
                                error: {
                                    code: 500,
                                    message: "Expected array or object as JSONP response",
                                },
                            };
                            response = null;
                        }

                        if (!error && response.error) {
                            error = response;
                            response = null;
                        }

                        callback.call(context, error, response);
                        window._EsriLeafletCallbacks[callbackId] = true;
                    }
                };

                callbacks++;

                return {
                    id: callbackId,
                    url: script.src,
                    abort() {
                        window._EsriLeafletCallbacks._callback[callbackId]({
                            code: 0,
                            message: "Request aborted.",
                        });
                    },
                };
            },
        },
    };

    // choose the correct AJAX handler depending on CORS support
    EsriLeaflet.get = (EsriLeaflet.Support.CORS) ? EsriLeaflet.Request.get.CORS : EsriLeaflet.Request.get.JSONP;

    // always use XMLHttpRequest for posts
    EsriLeaflet.post = EsriLeaflet.Request.post.XMLHTTP;

    // expose a common request method the uses GET\POST based on request length
    EsriLeaflet.request = EsriLeaflet.Request.request;

})(EsriLeaflet);

(function (EsriLeaflet) {

    // normalize request animation frame
    const raf = window.requestAnimationFrame ||
        window.webkitRequestAnimationFrame ||
        // @ts-ignore
        window.mozRequestAnimationFrame ||
        // @ts-ignore
        window.msRequestAnimationFrame ||
        function (cb) {
            return window.setTimeout(cb, 1000 / 60);
        };

    // convert an extent (ArcGIS) to LatLngBounds (Leaflet)
    EsriLeaflet.Util.extentToBounds = function (extent) {
        const sw = new L.LatLng(extent.ymin, extent.xmin);
        const ne = new L.LatLng(extent.ymax, extent.xmax);
        return new L.LatLngBounds(sw, ne);
    };

    // convert an LatLngBounds (Leaflet) to extent (ArcGIS)
    EsriLeaflet.Util.boundsToExtent = function (bounds) {
        bounds = L.latLngBounds(bounds);
        return {
            xmin: bounds.getSouthWest().lng,
            ymin: bounds.getSouthWest().lat,
            xmax: bounds.getNorthEast().lng,
            ymax: bounds.getNorthEast().lat,
            spatialReference: {
                wkid: 4326,
            },
        };
    };

    EsriLeaflet.Util.responseToFeatureCollection = function (response, idAttribute) {
        let objectIdField;

        if (idAttribute) {
            objectIdField = idAttribute;
        } else if (response.objectIdFieldName) {
            objectIdField = response.objectIdFieldName;
        } else if (response.fields) {
            for (let j = 0; j <= response.fields.length - 1; j++) {
                if (response.fields[j].type === "esriFieldTypeOID") {
                    objectIdField = response.fields[j].name;
                    break;
                }
            }
        } else {
            objectIdField = "OBJECTID";
        }

        const featureCollection = {
            type: "FeatureCollection",
            features: [],
        };
        const features = response.features || response.results;
        if (features.length) {
            for (let i = features.length - 1; i >= 0; i--) {
                featureCollection.features.push(EsriLeaflet.Util.arcgisToGeojson(features[i], objectIdField));
            }
        }

        return featureCollection;
    };

    // trim url whitespace and add a trailing slash if needed
    EsriLeaflet.Util.cleanUrl = function (url) {
        // trim leading and trailing spaces, but not spaces inside the url
        url = url.replace(/^\s+|\s+$|\A\s+|\s+\z/g, "");

        // add a trailing slash to the url if the user omitted it
        if (url[url.length - 1] !== "/") {
            url += "/";
        }

        return url;
    };

    EsriLeaflet.Util.requestAnimationFrame = L.Util.bind(raf, window);

    EsriLeaflet.Util.warn = function (message) {
        if (console && console.warn) {
            console.warn(message);
        }
    };

})(EsriLeaflet);

EsriLeaflet.Services.Service = L.Evented.extend({

    options: {
        proxy: false,
        useCors: EsriLeaflet.Support.CORS,
    },

    initialize(options) {
        options = options || {};
        this._requestQueue = [];
        this._authenticating = false;
        L.Util.setOptions(this, options);
        this.options.url = EsriLeaflet.Util.cleanUrl(this.options.url);
    },

    get(path, params, callback, context) {
        return this._request("get", path, params, callback, context);
    },

    post(path, params, callback, context) {
        return this._request("post", path, params, callback, context);
    },

    request(path, params, callback, context) {
        return this._request("request", path, params, callback, context);
    },

    metadata(callback, context) {
        return this._request("get", "", {}, callback, context);
    },

    authenticate(token) {
        this._authenticating = false;
        this.options.token = token;
        this._runQueue();
        return this;
    },

    _request(method, path, params, callback, context) {
        this.fire("requeststart", {
            url: this.options.url + path,
            params,
            method,
        });

        const wrappedCallback = this._createServiceCallback(method, path, params, callback, context);

        if (this.options.token) {
            params.token = this.options.token;
        }

        if (this._authenticating) {
            this._requestQueue.push([method, path, params, callback, context]);
        } else {
            const url = (this.options.proxy) ? this.options.proxy + "?" + this.options.url + path : this.options.url + path;

            if ((method === "get" || method === "request") && !this.options.useCors) {
                // eslint-disable-next-line new-cap
                return EsriLeaflet.Request.get.JSONP(url, params, wrappedCallback);
            } else {
                return EsriLeaflet[method](url, params, wrappedCallback);
            }
        }
    },

    _createServiceCallback(method, path, params, callback, context) {
        const self = this;
        // @ts-ignore
        return L.Util.bind(function (error, response) {

            if (error && (error.code === 499 || error.code === 498)) {
                self._authenticating = true;

                self._requestQueue.push([method, path, params, callback, context]);

                // fire an event for users to handle and re-authenticate
                self.fire("authenticationrequired", {
                    authenticate: L.Util.bind(self.authenticate, self),
                });

                // if the user has access to a callback they can handle the auth error
                error.authenticate = L.Util.bind(self.authenticate, self);
            }

            callback.call(context, error, response);

            if (error) {
                self.fire("requesterror", {
                    url: self.options.url + path,
                    params,
                    message: error.message,
                    code: error.code,
                    method,
                });
            } else {
                self.fire("requestsuccess", {
                    url: self.options.url + path,
                    params,
                    response,
                    method,
                });
            }

            self.fire("requestend", {
                url: self.options.url + path,
                params,
                method,
            });
        }, this);
    },

    _runQueue() {
        for (let i = this._requestQueue.length - 1; i >= 0; i--) {
            const request = this._requestQueue[i];
            const method = request.shift();
            this[method].apply(this, request);
        }
        this._requestQueue = [];
    },

});

EsriLeaflet.Services.service = function (params) {
    return new EsriLeaflet.Services.Service(params);
};

EsriLeaflet.Tasks.Task = L.Class.extend({

    options: {
        proxy: false,
        useCors: EsriLeaflet.Support.CORS,
    },

    // Generate a method for each methodName:paramName in the setters for this task.
    generateSetter(param, context) {
        const self = this;
        // @ts-ignore
        return L.Util.bind(function (value) {
            self.params[param] = value;
            return self;
        }, context);
    },

    initialize(endpoint) {
        // endpoint can be either a url (and options) for an ArcGIS Rest Service or an instance of EsriLeaflet.Service
        if (endpoint.request && endpoint.options) {
            this._service = endpoint;
            L.Util.setOptions(this, endpoint.options);
        } else {
            L.Util.setOptions(this, endpoint);
            this.options.url = EsriLeaflet.Util.cleanUrl(endpoint.url);
        }

        // clone default params into this object
        this.params = Object.assign({}, this.params || {});

        // generate setter methods based on the setters object implimented a child class
        if (this.setters) {
            for (const setter in this.setters) {
                if (!this.setters.hasOwnProperty(setter)) {
                    continue;
                }
                const param = this.setters[setter];
                this[setter] = this.generateSetter(param, this);
            }
        }
    },

    token(token) {
        if (this._service) {
            this._service.authenticate(token);
        } else {
            this.params.token = token;
        }
        return this;
    },

    request(callback, context) {
        if (this._service) {
            return this._service.request(this.path, this.params, callback, context);
        } else {
            return this._request("request", this.path, this.params, callback, context);
        }
    },

    _request(method, path, params, callback, context) {
        const url = (this.options.proxy) ? this.options.proxy + "?" + this.options.url + path : this.options.url + path;
        if ((method === "get" || method === "request") && !this.options.useCors) {
            // eslint-disable-next-line new-cap
            return EsriLeaflet.Request.get.JSONP(url, params, callback, context);
        } else {
            return EsriLeaflet[method](url, params, callback, context);
        }
    },
});
