import { deepmerge } from './CommonUtils';

/**
 * ES6 Leaflet compatibility class
 * allow to merge static options
 *
 * @class Klass
 */
export class Klass<O extends {} = any> {

    static options = {};

    static extend(props) {

        const NewClass = class extends this {};

        const proto = NewClass.prototype;

        // mix static properties into the class
        if (props.statics) {
            Object.assign(NewClass, props.statics);
            delete props.statics;
        }

        // mix includes into the prototype
        // if (props.includes) {

        //     Util.extend.apply(null, [proto].concat(props.includes));
        //     delete props.includes;
        // }

        // merge options
        if (props.options) {
            const options = proto.options ? Object.create(proto.options) : {};
            props.options = Object.assign({}, options, props.options || {});

            NewClass.options = props.options;
            delete props.options;
        }

        // mix given properties into the prototype
        Object.assign(proto, props);

        return NewClass;
    }

    // Includes a mixin into the current class.
    /** @deprecated */
    static include(props) {
        console.warn('Klass.include method is deprecated');
        Object.assign(this.prototype, props);
        return this;
    }

    options: O = {} as O;
    // private _initHooks: any = [];
    // private _initHooksCalled: boolean;

    constructor(...args: any[]) {
        // merge options
        // @ts-ignore
        const extendsOptions = Object.create(this.__proto__.options || {});
        const classOptions = [];
        // @ts-ignore
        let constr = this.__proto__;
        while (constr && constr.constructor && constr.constructor.options) {
            classOptions.push(constr.constructor.options);
            constr = constr.__proto__;
        }
        const opt = classOptions.reverse().reduce((a, b) => Object.assign(a, b), {});
        this.options = Object.assign(opt, extendsOptions, this.options);

        if (this.initialize) {
            this.initialize.apply(this, ...args);
        }
        if (this.optionsCallback) {
            this.options = deepmerge(this.options, this.optionsCallback());
        }
    }

    initialize?(): void;
    optionsCallback?(): O;

}
