import L from 'leaflet';
import 'leaflet-i18n/Leaflet.i18n';
import { htmlTemplate } from './GeocoderUtils';
import './GeosearchSettingsControl.scss';

const DomEvent = L.DomEvent;
const DomUtil = L.DomUtil;

export class GeosearchSettingsControl {

    _geosearch;
    _isOpen = false;

    _controlBlock;
    _settingsBtn;
    _cancelBtn;

    _controlIco;

    _container: HTMLElement;
    _settingsBlock: HTMLElement;
    _allProviders: any[];
    _applyBtn: HTMLElement;

    constructor(geosearch) {
        this._geosearch = geosearch;
        this._container = this._createContainer();
        this._allProviders = this._geosearch.options.providers.slice();
    }

    getContainer() {
        return this._container;
    }

    destroy() {
        this._removeEventsListeners();
    }

    show() {
        this._container.style.display = 'block';
    }

    hide() {
        this._container.style.display = 'none';
        this.closeSettings();
    }

    getControlIco() {
        this._controlIco = DomUtil.create('div', 'geocoder-control-input-ico settings-ico');
        this._controlIco.title = L._('Open search settings');
        return this._controlIco;
    }

    closeSettings() {
        const isOpened = this._isOpen;
        this._isOpen = false;
        if (isOpened) {
            L.DomUtil.remove(this._controlBlock);
            L.DomUtil.remove(this._settingsBlock);
            const inputValue = this._geosearch._input.value;
            if (inputValue) {
                this._geosearch._input.focus();
            }
            DomUtil.empty(this._settingsBlock);
            this._logSettings();
        }
    }

    _logSettings() {
        const torus = this._geosearch.options.torus;
        const status = {};
        this._allProviders.forEach((x) => {
            status[x.options.label] = this._enabledProviderIndex(x) !== -1;
        });
        torus._fireClientLogEvent('geosearch-settings:change', { status });
    }

    _toggleSettings() {
        if (this._isOpen) {
            this.hide();
        } else {
            this.show();
            this._openSettings();

        }
    }

    _createContainer() {
        const container = DomUtil.create('div', 'geocoder-control-suggestions leaflet-bar');
        return container;
    }

    _openSettings() {
        this._isOpen = true;
        this._geosearch._clearValues();
        this._container.appendChild(this._createControlBlock());
        this._container.appendChild(this._createSettingsBlock());
        this._updateControlBlock();
        this._displaySettingsBlock();
    }

    _createSettingsBtn({ html, addClass }: {html?: string, addClass?: string}) {
        let className = 'geocoder-control-suggestion geosearch-settings__controlbtn ';
        if (addClass) {
            className += ' ' + addClass;
        }
        const btn = DomUtil.create('div', className);
        btn.innerHTML = html;
        return btn;
    }

    _onApplyBtnClick() {
        this.closeSettings();
    }

    _onCancelBtnClick() {
        this.closeSettings();
    }

    _onSettingsBtnClick() {
        this._openSettings();
    }

    _createOpenSettingsBtn() {
        this._settingsBtn = this._createSettingsBtn({ html: L._('Settings') });
        DomEvent.on(this._settingsBtn, 'click', this._onSettingsBtnClick, this);
        return this._settingsBtn;
    }

    _createApplyBtn() {
        this._applyBtn = this._createSettingsBtn({ html: L._('Apply') });
        DomEvent.on(this._applyBtn, 'click', this._onApplyBtnClick, this);
        return this._applyBtn;
    }

    _createCancelBtn() {
        this._cancelBtn = this._createSettingsBtn({ html: L._('Close settings') });
        DomEvent.on(this._cancelBtn, 'click', this._onCancelBtnClick, this);
        return this._cancelBtn;
    }

    _createSettingsBlock() {
        this._settingsBlock = DomUtil.create('div', 'geosearch-settings__settings-block');
        return this._settingsBlock;
    }

    _enabledProviderIndex(provider) {
        return this._geosearch.options.providers.indexOf(provider);
    }

    _displaySettingsBlock() {
        DomUtil.empty(this._settingsBlock);
        this._allProviders.forEach((x) => {
            const isChecked = this._enabledProviderIndex(x) !== -1;
            let providerSettings = null;
            if (x.getSettingsControl) {
                providerSettings = x.getSettingsControl();
            }
            const input = htmlTemplate(`
                <input
                    class="form-check-input"
                    type="checkbox"
                    name=${x.options.label}
                >`,
            ) as HTMLInputElement;
            if (isChecked) {
                input.checked = true;
            }
            const checkbox = htmlTemplate(`
                <div class="form-check">
                    {input}
                    <label class="form-check-label" for=${x.options.label}>
                        ${x.options.label}
                    </label>
                    ${providerSettings ? '{providerSettings}' : ''}
                </div>`, { providerSettings, input },
            );
            DomEvent.on(input, 'change', () => {
                const checked = input.checked;
                const index = this._enabledProviderIndex(x);
                if (checked) {
                    if (index === -1) {
                        // right order
                        const newProviders = this._allProviders.filter((y) => {
                            return this._enabledProviderIndex(y) !== -1 || y === x;
                        });
                        this._geosearch.options.providers = newProviders;
                    }
                } else if (index !== -1) {
                    this._geosearch.options.providers.splice(index, 1);
                }
            }, this);
            this._settingsBlock.appendChild(checkbox);
        });
    }

    _createControlBlock() {
        this._controlBlock = DomUtil.create('div', 'geosearch-settings__control-container');
        return this._controlBlock;
    }

    _updateControlBlock() {
        if (this._controlBlock) {
            DomUtil.empty(this._controlBlock);
            if (this._settingsBtn) {
                DomEvent.off(this._settingsBtn, 'click', this._onSettingsBtnClick, this);
            }
            if (this._applyBtn) {
                DomEvent.off(this._applyBtn, 'click', this._onApplyBtnClick, this);
            }
            if (this._cancelBtn) {
                DomEvent.off(this._cancelBtn, 'click', this._onCancelBtnClick, this);
            }
            if (this._isOpen) {
                // this._controlBlock.appendChild(this._createApplyBtn());
                this._controlBlock.appendChild(this._createCancelBtn());
            } else {
                // this._controlBlock.appendChild(this._createOpenSettingsBtn());
            }
        }
    }

    addEventsListeners() {
        DomEvent.on(this._controlIco, 'click', this._toggleSettings, this);
        // DomEvent.on(this._geosearch._wrapper, "mouseenter", this.show, this);
        // DomEvent.on(this._geosearch._wrapper, "mouseleave", this.hide, this);
    }

    _removeEventsListeners() {
        DomEvent.on(this._controlIco, 'click', this._toggleSettings, this);
        // DomEvent.off(this._geosearch._wrapper, "mouseenter", this.show, this);
        // DomEvent.off(this._geosearch._wrapper, "mouseleave", this.hide, this);
    }
}
