import { LatLng, LatLngBounds } from 'leaflet';
import L from 'leaflet';
import { CustomProvider, IGeocodeResult, IGeocoderProviderOptions } from '../../../packages/geocod-ex/src/js/Geocoder/CustomProvider';
import { Yandex } from '../../../packages/geocod-ex/src/js/Geocoder/Providers/YandexProvider';
import { TorusMap } from '../../../packages/torus-map/TorusMap';
import { createGetSrcPromise } from '../../../packages/torus-map/TrotilExt';
import { post } from './utils';

declare global {
  interface Window {
    ymaps: any;
  }
}

interface IYandexSuggestResponceItem {
  type: 'geo'; // TODO: place other
  displayName: string;
  value: string;
}

interface IGwiYandexGeocodeResponseItem {
  name: string;
  latlng: [number, number];
  bounds: [[number, number], [number, number]];
}

interface SuggersApiQueryOptions {
  boundedBy?: [[number, number], [number, number]];
  provider?: string;
}

// Read descriptions https://tech.yandex.ru/maps/doc/geocoder/desc/concepts/input_params-docpage/
interface IYandexGeocodeParams {

  /** @default 'longlat' */
  sco?: 'longlat' | 'latlong';
  kind?: 'house' | 'street' | 'metro' | 'district' | 'locality';

  type?: string;

  /**
   * bbox=x1,y1~x2,y2
   * @example 36.6,54.6~38.6,56.6
   */
  bbox?: string;

  /** @example 37.618920,55.756994 */
  ll?: string;
  /** @example 0.552069,0.400552 */
  spn?: string;

  skip?: number;
  lang?: 'ru_RU' | 'uk_UA' | 'be_BY' | 'en_RU' | 'en_US' | 'tr_TR';

}

interface IGeocodeQueryParams {
  address: string;
  mode: 'mass' | 'single';
  options?: IYandexGeocodeParams;
}

interface IGwiYandexOptions {
  serviceUrl: string;
  reverseUrl?: string;
  suggestUrl: string;
  // key: '',
  apiUrl?: string;
  getUrl?: string;
  torus?: TorusMap;
  provider?: string;
}

export class GwiYandex extends Yandex<IGwiYandexOptions> {
  static options: IGwiYandexOptions = {
    serviceUrl: 'ServiceGEOCODE.asmx/GetGeoObjectsByAddress',
    reverseUrl: 'ServiceGEOCODE.asmx/GetGeoObjectsByCoordinates',
    suggestUrl: 'http://suggest-maps.yandex.ru/suggest-geo',
    // key: '',
    // apiUrl: 'https://enterprise.api-maps.yandex.ru/2.1/?lang=ru_RU',
    getUrl: 'ServiceGEOCODE.asmx/GetApiUrl',
    // provider: 'yandex#search',
  };

  private _nameValueAlias: { [name: string]: string } = {};

  geocode(query, bounds, cb, context, key, options) {
    return this._geocode(this.options.serviceUrl, query, bounds, cb, context, key, options);
  }

  suggest(query: string,
          bounds: LatLngBounds,
          cb: (results: IGeocodeResult[]) => any,
          context: any) {
    // if (!this.options.key) {
    //   return super.suggest(query, bounds, cb, context);
    // } else {
    const suggestOpt: SuggersApiQueryOptions = {};
    if (bounds) {
      const sw = bounds.getSouthWest();
      const ne = bounds.getNorthEast();
      suggestOpt.boundedBy = [[sw.lat, sw.lng], [ne.lat, ne.lng]];
    }
    return this._apiSuggest(query, suggestOpt).then((results) => {
      cb.call(context, results);
    }).catch(() => {
      cb.call(context, []);
    });
    // }
  }

  reverse(location: LatLng, scale: number, cb: (results: IGeocodeResult[]) => void, context: any, options?) {
    const { lat, lng } = location;
    const query = [lat, lng].join(',');
    this._geocode(this.options.reverseUrl, query, null, cb, context, null, options);
  }

  _geocode(url, query, bounds: L.LatLngBounds, cb, context, key?, options?) {

    if (this._nameValueAlias[query]) {
      query = this._nameValueAlias[query];
    }

    const params: IGeocodeQueryParams = {
      address: query,
      mode: (options && options.type) || 'single',
      options: {},
    };
    const torus: TorusMap = this.options.torus;

    if (bounds && params.mode === 'single' && bounds.getCenter) {
      const { lat, lng } = bounds.getCenter();
      params.options.ll = [lng, lat].join(',');
    }
    const type = key && key.type;
    if (type) {
      params.options.type = type;
    }

    return this._post(url, { data: params, format: 'json', withCredentials: (torus && torus.post ? false : true), method: 'POST' } ).then((data: IGwiYandexGeocodeResponseItem[]) => {
      if (data) {
        const results = [];
        const featureMember = data;

        if (featureMember.length > 0) {
          for (let fry = 0; fry <= featureMember.length; fry++) {
            const res = featureMember[fry];
            if (res) {
              const lowerCorner = res.bounds[0];
              const upperCorner = res.bounds[1];

              results[fry] = {
                name: res.name,
                bounds: new LatLngBounds([lowerCorner[1], lowerCorner[0]],
                  [upperCorner[1], upperCorner[0]]),
                latlng: new LatLng(res.latlng[1], res.latlng[0]),
              };
            }
          }
        }
        cb.call(context, results);
      } else {
        throw new Error('Out of limit');
      }
    });
  }

  _apiQuery(query: string, options?: SuggersApiQueryOptions): Promise<IGeocodeResult[]> {
    const ymaps = window.ymaps;
    const provider = this.options.provider;
    if (provider) {
      options.provider = provider;
    }
    if (this._nameValueAlias[query]) {
      query = this._nameValueAlias[query];
    }
    return ymaps.suggest(query, options).then((data: IYandexSuggestResponceItem[]) => {
      const toReturn: IGeocodeResult[] = [];
      data.forEach((x) => {
        this._nameValueAlias[x.displayName] = x.value;
        toReturn.push({ name: x.displayName, magicKey: x });
      });
      return toReturn;
    });
  }

  _apiSuggest(query, opt: SuggersApiQueryOptions): Promise<IGeocodeResult[]> {
    if (window.ymaps) {
      return this._apiQuery(query, opt);
    } else {
      // const apiSourceUrl = this.options.apiUrl +
      //   (this.options.apiUrl.match(/\?/) ? '&' : '?') + 'apikey=' + this.options.key;

      const torus: TorusMap = this.options.torus;

      return this._post(this.options.getUrl, { withCredentials: (torus && torus.post ? false : true), format: 'json', method: 'POST' }).then((apiSourceUrl: string) => {
        if (apiSourceUrl && typeof apiSourceUrl === 'string') {
          return createGetSrcPromise(apiSourceUrl)
            .then(() => {
              return new Promise<IGeocodeResult[]>((resolve) => {

                window.ymaps.ready((data) => {
                  resolve(this._apiQuery(query, opt));
                });
              });
            })
            .catch((er) => {
              return er;
            });
        } else {
          return Promise.reject(new Error('Out of limit'));
        }
      });
    }
  }

  private _post(url: string, params?: Record<string, any>): Promise<any> {
    const torus: TorusMap = this.options.torus;
    if (torus && torus.post) {
      return torus.post(url, params );
    } else {
      return post(url, params ); // && { data: JSON.stringify(params) }
    }
  }
}

interface CustomProviderOptions extends IGeocoderProviderOptions {
  limitMessage?: string;
}
export class GwiYandexProvider extends CustomProvider<CustomProviderOptions> {
  static options: CustomProviderOptions = {
    label: 'Yandex',
    Geocoder: GwiYandex,
    excludeRegexp: '(^:)|(^[a-zA-Z]\\w+:)|(^[a-zA-Z]{2}\\d{2})|\\b(\\+7|7|8)?([ \\(\\)\\-]*9)(([ \\(\\)\\-]*\\d){9})\\b',  // HINT: Exclude ServiceMapProvider and LBSProvider queries. See bug 60448. RCH 2020-08-19
  };

  checkLimit(result) {
    return result === 'Out of limit';
  }

  results(text: string,
          key: any,
          bounds: L.LatLngBounds,
          callback: (err: Error | string, results: IGeocodeResult) => void,
          options?: any,

  ) {
    const request = super.results(text, key, bounds, callback, options) as any;
    request.catch((er) => {
      const isLimit = this.checkLimit(er.message);
      if (isLimit) {
        const limitMessage = this.options.limitMessage || L._('The limit for {label} geocoding provider is reached');
        const error = L.Util.template(limitMessage, this.options);
        this.options.torus.noticeManager.error(error);
      }
    });
    return request;
  }
}
