import { GeoJsonObject } from 'geojson';
import L, { LatLngBounds } from 'leaflet';
import { Klass } from '../../../../../common/Klass';
import { TorusMap } from '../../../../torus-map/TorusMap';
import { Geosearch } from '../Geosearch';
import { geocoderServiceSuggestFunc, ICustomGeocoderService, IGeocoderServiceConstructor } from './CustomService';
import versionJson from '../../../../../version.json';

export const Geocoder: { [x: string]: any } = {
    callbackId: 0,

    getJSON(url, params, callback, context?, jsonpParam?) {
        const xmlHttp = new XMLHttpRequest();
        xmlHttp.onreadystatechange = function () {
            if (xmlHttp.readyState !== 4) {
                return;
            }
            if (xmlHttp.status !== 200 && xmlHttp.status !== 304) {
                console.log(`Error: ${xmlHttp.status} for URL ${url}`);
                callback('');
                return;
            }
            try {
                callback(JSON.parse(xmlHttp.response));
            } catch (er) {
                console.warn(er);
                callback('');
            }

        };

        const torus = context && context.options && context.options.torus;
        if (torus && torus.options && torus.options.isWiget) {
            // this is widget, so add app version to request params
            params.source = 'widget_' + versionJson.number;
        }

        xmlHttp.open('GET', url + L.Util.getParamString(params), true);
        xmlHttp.setRequestHeader('Accept', 'application/json');
        xmlHttp.send(null);
        return xmlHttp;
    },

    getJson(url, params, callback) {
        const xmlHttp = new XMLHttpRequest();
        xmlHttp.onreadystatechange = function () {
            if (xmlHttp.readyState !== 4) {
                return;
            }
            if (xmlHttp.status !== 200 && xmlHttp.status !== 304) {
                callback('');
                return;
            }
            callback(JSON.parse(xmlHttp.response));
        };
        xmlHttp.open('GET', url + L.Util.getParamString(params), true);
        xmlHttp.setRequestHeader('Accept', 'application/json');
        xmlHttp.setRequestHeader('Content-Type', 'application/json');
        xmlHttp.send(null);
        return xmlHttp;
    },

    jsonp(url, params, callback, context, jsonpParam) {
        const callbackId = '_l_geocoder_' + (Geocoder.callbackId++);
        params[jsonpParam || 'callback'] = callbackId;
        window[callbackId] = L.Util.bind(callback, context);
        const script = document.createElement('script');
        script.type = 'text/javascript';
        script.src = url + L.Util.getParamString(params);
        script.id = callbackId;
        document.getElementsByTagName('head')[0].appendChild(script);
    },
};

export interface IGeocoderProviderOptions {
    enabled?: boolean;
    label?: string;
    exactlyRegexp?: RegExp | string;
    // not used. not tested
    excludeRegexp?: RegExp | string;
    // not used. not tested
    includeRegexp?: RegExp | string;
    resultLimit?: number;
    Geocoder?: IGeocoderServiceConstructor;
    memoryCacheTime?: number;
    // prefer use without matching
    preferUse?: boolean;

    torus?: TorusMap;
}

export interface IGeocodeResult {
    name?: string;
    mode?: string;
    latlng?: L.LatLng;
    bounds?: string | LatLngBounds;
    geojson?: GeoJsonObject;
    additional?: { [x: string]: any };

    /**
     * @deprecated use name everywhere
     */
    text?: string;
    magicKey?: any;
}

/**
 * BASE class for custom geocoder providers
 */
export class CustomProvider<O extends IGeocoderProviderOptions = IGeocoderProviderOptions> extends Klass<O> {

    static options: IGeocoderProviderOptions = {
        label: 'Custom',
        exactlyRegexp: '',
        excludeRegexp: '',
        includeRegexp: '',
        memoryCacheTime: 1000 * 60 * 60,
        preferUse: false,
    };

    Geocoder: ICustomGeocoderService;

    private suggest?: geocoderServiceSuggestFunc;
    private _separateSuggest?: boolean;
    private _results: IGeocodeResult[];

    constructor(options) {
        super();
        L.setOptions(this, options);
        // console.log(`Provider ${this.constructor.name} is created`);
        if (this.options.Geocoder) {
            this.Geocoder = new this.options.Geocoder(this.options);
        } else {
            throw new Error('Geocoder engine is not set');
        }

        if (this.Geocoder.suggest) {
            this.suggest = this.Geocoder.suggest.bind(this.Geocoder);
            this._separateSuggest = true;
        } else if (this.Geocoder.geocode) {
            this.suggest = this.Geocoder.geocode.bind(this.Geocoder);
        }
        // force remove methods from class if provider is not support this operations
        // to make something like this:
        // if (provider.reverse) {...}
        if (!this.Geocoder.geocode) {
            this.results = null;
        }
        if (!this.Geocoder.reverse) {
            this.reverse = null;
        }
    }

    checkLimit?(result: any): boolean;

    suggestions(text: string,
                bounds: L.LatLngBounds,
                callback: (err: Error | string, results: IGeocodeResult[]) => void): void {
        const self = this;
        if (this._checkQueryApproach(text)) {
            return this.suggest(text, bounds, function (results) {
                if (results) {
                    if (self.options.resultLimit && (results.length > self.options.resultLimit)) {
                        results = results.slice(0, self.options.resultLimit);
                    }
                    self._geocodeResult(results);
                }
                callback('', results);
            }, this, this.options);
        }
        return callback('', []);
    }

    results(text: string,
            key: any,
            bounds: L.LatLngBounds,
            callback: (err: Error | string, results: IGeocodeResult) => void,
            options?: any,
    ) {
        if (this._checkQueryApproach(text)) {
            if (!this._separateSuggest && this._results && this._results.length) {
                const index = this._results.map(function (r) {
                    return r.name;
                }).indexOf(text);
                if (index !== -1) {
                    return callback('', this._results[index]);
                }
            }
            console.log(`Geocode using ${this.Geocoder.constructor.name}`);
            return this.Geocoder.geocode(text, bounds, function (results) {
                if (results) {
                    return callback('', results[0]);
                } else {
                    return callback('', null);
                }
            }, this, key, options);
        }
        return callback('', null);
    }

    reverse(
        coord: L.LatLng,
        zoom: number | null,
        callback: (err: Error | string, results) => void,
        options?: any,
    ) {
        if (this.Geocoder && this.Geocoder.reverse) {
            this.Geocoder.reverse(coord, zoom, function (results) {
                if (results) {
                    callback('', results);
                } else {
                    callback('', false);
                }
            }, this, options);
        }
    }

    isQueryExactlyForThisProvider(text) {
        if (text.match && !!this.options.exactlyRegexp) {
            return !!text.match(this.options.exactlyRegexp);
        }
        return false;
    }

    _checkQueryApproach(text) {
        text = String(text);
        let isApproach = true;
        if (this.options.excludeRegexp) {
            isApproach = !text.match(this.options.excludeRegexp);
        }
        if (isApproach && this.options.includeRegexp) {
            return text.match(this.options.includeRegexp);
        }
        return isApproach;
    }

    _geocodeResult(results) {
        for (let fry = results.length; fry--;) {
            const res = results[fry];
            for (let f = 0; f < results.length; f++) {
                if (fry !== f && res.name === results[f].name) {
                    res.name += '*';
                }
            }
        }
        this._results = results;
    }
}

Geosearch.Providers.CustomProvider = CustomProvider;
