import L from 'leaflet';
import '../../Geosearch';
import { CustomProvider, Geocoder } from '../CustomProvider';
import { CustomGeocoder } from '../CustomService';

class OpenStreetMapRu extends CustomGeocoder {
    static options = {
        serviceUrl: 'http://openstreetmap.ru/api/',
    };

    geocode(query, bounds, cb, context, key) {
        const params: any = { q: query, st: 'addr' };
        const onGeocode = function (data) {
            if (data && data.find) {
                const featureMember = data.matches;
                const results = [];
                const res = featureMember[0];
                const name = res && (res.display_name || res.name);
                if (name) {
                    results[0] = {
                        name,
                        latlng: new L.LatLng(res.lat, res.lon),
                    };
                    if (res.geojson) {
                        results[0].geojson = JSON.parse(res.geojson);
                    }
                }
                cb.call(context, results);
            } else {
                cb.call(context, false);
            }
        };
        if (key && key.lat && key.lng) {
            params.lat = key.lat;
            params.lon = key.lng;
        } else if (typeof key === 'number' || typeof key === 'string') {
            return Geocoder.getJSON(this.options.serviceUrl + 'searchid', { id: key }, onGeocode);
        }
        return Geocoder.getJSON(this.options.serviceUrl + 'search', params, onGeocode);
    }

    suggest(query, bounds, cb, context) {
        const params: any = { q: query };
        if (bounds) {
            params.bbox = [bounds.getWest(), bounds.getSouth(), bounds.getEast(), bounds.getNorth()].join(',');
        }

        return Geocoder.getJSON(this.options.serviceUrl + 'autocomplete', params, function (data) {
            const results = [];
            if (data && data.find) {
                const featureMember = data.matches;

                for (let fry = 0; fry <= featureMember.length; fry++) {
                    const res = featureMember[fry];
                    if (res && res.name) {
                        const latLng = new L.LatLng(res.lat, res.lon);
                        results.push({
                            name: res.name,
                            latlng: latLng,
                            magicKey: res.id || latLng,
                        });
                    }
                }

            }
            cb.call(context, results);
        });

    }

    reverse(location, scale, cb, context) {
        Geocoder.getJSON(this.options.serviceUrl + 'searchlola', Object.assign({
            lat: location.lat,
            lon: location.lng,

        }, this.options.reverseQueryParams), function (data) {
            const result = [];
            let loc;
            if (data && data.matches && data.matches.length) {
                data = data.matches[0];  // @HINT: there is only one result row
                if (data && data.lat && data.lon) {
                    loc = L.latLng(data.lat, data.lon);
                    result.push({
                        name: data.display_name,
                        latlng: loc,
                        bounds: L.latLngBounds(loc, loc),
                        properties: data,
                    });
                }
            }

            cb.call(context, result);
        }, this, 'json_callback');
    }
}

Geocoder.OpenStreetMapRu = OpenStreetMapRu;

export class OpenStreetMapRuProvider extends CustomProvider {
    static options = {
        label: 'OSM-RU',
        Geocoder: OpenStreetMapRu,
    };
}
