import L from 'leaflet';
import '../../Geosearch';
import { CustomProvider, Geocoder } from '../CustomProvider';
import { CustomGeocoder } from '../CustomService';

export class GoogleGeocoder extends CustomGeocoder {
    static options = {
        serviceUrl: 'https://maps.googleapis.com/maps/api/geocode/json',
    };

    private _key: string;

    constructor(key) {
        super({});
        this._key = key;
    }

    geocode(query, bounds, cb, context) {
        const params: any = {
            address: query,
        };
        if (this._key && this._key.length) {
            params.key = this._key;
        }

        return Geocoder.getJSON(this.options.serviceUrl, params, function (data) {
            const results = [];
            let loc;
            let latLng;
            let latLngBounds;
            if (data.results && data.results.length) {
                for (let i = 0; i <= data.results.length - 1; i++) {
                    loc = data.results[i];
                    latLng = L.latLng(loc.geometry.location);
                    latLngBounds = L.latLngBounds(L.latLng(loc.geometry.viewport.northeast),
                        L.latLng(loc.geometry.viewport.southwest));
                    results[i] = {
                        name: loc.formatted_address,
                        bounds: latLngBounds,
                        latlng: latLng,
                    };
                }
            }
            cb.call(context, results);
        });
    }

    reverse(location, scale, cb, context) {
        const params: any = {
            latlng: encodeURIComponent(location.lat) + ',' + encodeURIComponent(location.lng),
        };
        if (this._key && this._key.length) {
            params.key = this._key;
        }
        Geocoder.getJSON(this.options.serviceUrl, params, function (data) {
            const results = [];
            let loc;
            let latLng;
            let latLngBounds;
            if (data.results && data.results.length) {
                for (let i = 0; i <= data.results.length - 1; i++) {
                    loc = data.results[i];
                    latLng = L.latLng(loc.geometry.location);
                    latLngBounds = L.latLngBounds(L.latLng(loc.geometry.viewport.northeast),
                        L.latLng(loc.geometry.viewport.southwest));
                    results[i] = {
                        name: loc.formatted_address,
                        bounts: latLngBounds,
                        latlng: latLng,
                    };
                }
            }

            cb.call(context, results);
        });
    }
}

Geocoder.Google = GoogleGeocoder;

export class GoogleProvider extends CustomProvider {
    static options = {
        label: 'Google',
        Geocoder: GoogleGeocoder,
    };

    constructor(options) {
        super(Object.assign({}, GoogleProvider.options, options));
    }
}
