import L from 'leaflet';
import 'leaflet-i18n/Leaflet.i18n';
import { MapControl } from '../../../../common/MapControl';
import { Type } from '../../../../common/Type';
import { loop } from '../../../torus-map/TrotilExt';
import { CustomProvider, IGeocodeResult } from './Geocoder/CustomProvider';
import { EsriLeafletGeocoding } from './Geocoder/EsriLeafletGeocoding';
import { GeosearchSettingsControl } from './GeosearchSettingsControl';
import { TorusMap } from '../../../torus-map/TorusMap';

function escapeRegExp(re) {
    return re.replace(/[.+*?|^$[\]{}()\\]/g, '\\$&');
}

export interface IGeosearchOptions {
    position?: L.ControlPosition;
    placeholder?: string;
    title?: string;
    mapAttribution?: string;
    minInputLength?: number;
    inputDelay?: number;
    defaultSuggestionLayerStyle?: L.PathOptions;
    hoverSuggestionLayerStyle?: L.PathOptions;
    zoomToResult?: boolean;
    /* for suggestions hover **/
    autoZoom?: boolean;
    useMapBounds?: number | boolean;
    /** shortcut for always expanded input block, controls: expanded, collapseOnCloseBtn and collapseAfterResult */
    alwaysExpanded?: boolean;
    expanded?: boolean;
    collapseOnCloseBtn?: boolean;
    collapseAfterResult?: boolean;
    /** remove suggestions after result action, not work if collapseAfterResult is true */
    clearAfterResult?: boolean;
    saveInputAfterResult?: boolean;
    useSuggestTextToSaveInput?: boolean;
    clearOnBlur?: boolean;
    forStorage?: boolean; // TODO: investigate and clear remove
    allowMultipleResults?: boolean; // do not use yet
    maxResults?: number;
    popupMinWidth?: number;
    providers?: CustomProvider[];

    displaySettings?: boolean;
    showMapSuggestion?: boolean;
    disableClickPropagation?: boolean;

    isWiget?: boolean;

    torus?: TorusMap;
}

export interface IGeosearchResults {
    bounds?: L.LatLngBounds;
    latlng?: L.LatLng;
    name?: string;
    text?: string;
    results?: IGeocodeResult[];
    provider?: CustomProvider;
    timestamp?: Date;
    options: any;
}

export interface IGeocodeResultOptions {
    zoomToResult?: boolean;
    zoom?: boolean;
    silent?: boolean;
    provider?: CustomProvider;
    success?: () => void;
    error?: () => void;
}

interface IGeocoderControlSuggestion extends HTMLLIElement {
    provider?: CustomProvider;
    name?: string;
}

export class Geosearch extends MapControl<IGeosearchOptions> {

    static Providers: { [providerName: string]: Type<CustomProvider> } = {};

    static options = {
        position: 'topleft',
        placeholder: 'Search for places or addresses',
        title: 'Location Search',
        mapAttribution: 'Geocoding',
        minInputLength: 3,
        inputDelay: 750,
        defaultSuggestionLayerStyle: { weight: 3, opacity: 0.4, fillOpacity: 0.2 },
        hoverSuggestionLayerStyle: { weight: 4, opacity: 0.7, fillOpacity: 0.3 },

        zoomToResult: true,
        autoZoom: false, // for suggestions hover
        useMapBounds: 21,

        /** shortcut for always expanded input block, controls: expanded, collapseOnCloseBtn and collapseAfterResult */
        alwaysExpanded: false,
        expanded: false,
        collapseOnCloseBtn: true,
        collapseAfterResult: true,
        /** remove suggestions after result action, not work if collapseAfterResult is true */
        clearAfterResult: true,
        saveInputAfterResult: false,
        clearOnBlur: true, // @HINT: enabled due to bug 60370. RCH 2020-06-29

        forStorage: false, // TODO: investigate and clear remove
        allowMultipleResults: false, // do not use yet

        popupMinWidth: 350,

        providers: [],

        displaySettings: true,
        showMapSuggestion: true,
        disableClickPropagation: true,

        isWiget: false,
    };

    private featureLayers: L.FeatureGroup;
    private resultFeatureLayers: L.FeatureGroup;

    private _mapBounds: L.LatLngBounds;
    private _pendingSuggestions;
    private _expanded: boolean;
    private _suggestions: HTMLElement;
    private _input: HTMLInputElement;
    private _wrapper: HTMLElement;
    private _ico: HTMLElement;
    private _changeIntervalId: number;
    private _settingsControl: GeosearchSettingsControl;

    constructor(options) {
        super(options);
        const self = this;
        // L.Util.setOptions(this, options);
        // Temp for suggestions
        this.featureLayers = L.featureGroup();
        this.resultFeatureLayers = L.featureGroup();
        this._mapBounds = null;

        this._pendingSuggestions = [];
        // will be defined in onAdd method late
        this._expanded = null;

        if (this.options.alwaysExpanded) {
            this.options.expanded = true;
            this.options.collapseOnCloseBtn = false;
            this.options.collapseAfterResult = false;
        }
        this._settingsControl = new GeosearchSettingsControl(this);
        this._forAllProviders(function (p) {
            p = p || [];
            if (self.options.maxResults) {
                for (let i = 0; i < p.length; i++) {
                    p[i].options.maxResults = self.options.maxResults;
                }
            }
        });
    }

    getProviderLabel(p) {
        return p.options.label || (p.Geocoder && p.Geocoder.options.label);
    }

    getSuggestions() {
        return this._suggestions;
    }

    getInput() {
        return this._input;
    }

    setInputText(text: string) {
      this._input.value = text;
    }

    _forAllProviders(func) {
        for (const p in this.options.providers) {
            if (this.options.providers.hasOwnProperty(p)) {
                func.call(this, this.options.providers[p]);
            }
        }
    }

    _getExactProviders(text) {
        let providers = [];
        let fry;
        let p;
        for (fry = 0; fry < this.options.providers.length; fry++) {
            p = this.options.providers[fry];
            if ((p.isQueryExactlyForThisProvider && p.isQueryExactlyForThisProvider(text)) && !p.options.preferUse) {
                providers.push(p);
            }
        }
        if (!providers.length) {
            providers = this.options.providers;
        } else {
            const alwaysProviders = this.options.providers.filter((item) => !!item.options.preferUse);
            if (alwaysProviders.length > 0) {
                providers.push(...alwaysProviders);
            }
        }
        return providers;
    }

    _geocode(text: string,
             key: any,
             provider?: CustomProvider,
             success?: () => void,
             error?: () => void,
             options?: IGeocodeResultOptions): Promise<IGeosearchResults | Error> {
        options = options || {};
        text = text.trim();
        const textLength = text.length;
        const self = this;
        const zoomToResult = options.zoomToResult !== undefined ? options.zoom : this.options.zoomToResult;
        if (this.options.clearAfterResult) {
            this.clear();
        }
        if (this.options.useSuggestTextToSaveInput) {
            this._input.value = text;
        }
        const providers = text ? this._getExactProviders(text) : [];
        return new Promise(function (resolve, reject) {
            let activeRequests = 0;
            let allResults = [];
            let bounds;
            const callback = function (err, results) {
                activeRequests--;
                if (results) {
                    allResults = allResults.concat(results);
                }
                if (activeRequests <= 0) {
                    if (allResults.length) {
                        bounds = self._boundsFromResults(allResults);
                        const data: IGeosearchResults = {
                            results: allResults,
                            bounds,
                            // @HINT: bounds center is passed to latlng to show center of many objects on the map (seems this is old behavior and it is not in use anymore).
                            // However, result handler function GeocoderManager._onSearchControlResult() uses allResults[ix].latlng to show object on the map.
                            // See bug 60206. RCH 2020-07-09
                            latlng: bounds ? bounds.getCenter() : undefined,
                            text, // TODO:  save remove
                            name: text,
                            provider: provider ? provider : ( results && results.provider ? results.provider : undefined ),  // @HINT: results.provider is passed by function geocode() of LBSGeocoder
                            options,
                        };
                        if (!options.silent) {
                            self.fire('results', data);
                        }
                        resolve(data);
                        if (allResults.length === 1 && allResults[0].latlng && allResults[0].panOnly) {
                            self._map.panTo(allResults[0].latlng);
                        } else if (zoomToResult && bounds) {
                            self._map.fitBounds(bounds);
                        }
                        if (success) {
                            success();
                        }
                    } else {
                        if (self.options.torus) {
                            reject('No results');
                            if (error) {
                              error();
                            }
                        } else {
                          // this is widget, provide specific handler for results error
                          self.fire('results-error', { name, provider, error: err });
                          if (error) {
                            error();
                          }
                          reject('No results');
                        }
                    }
                    self._stopLoading();
                    if (!options.silent) {
                        self.fire('load');
                    }
                }
            };

            if (textLength >= self.options.minInputLength) {
                if (typeof key === 'string') {
                    activeRequests++;
                    self._fireSendResultRequest({ provider });
                    provider.results(text, key, self._searchBounds(), callback, options);
                } else {
                    const activeProvider = providers;
                    activeRequests = !provider ? activeProvider.length : activeRequests;
                    if (activeProvider.length) {
                        let providerFound = false;
                        for (let i = 0; i < activeProvider.length; i++) {
                            const ap = activeProvider[i];
                            if (provider) {
                                const isProvidersLabelExist = provider.options && provider.options.label &&
                                    ap && ap.options && ap.options.label;
                                const isRightProvider = provider.options.label === ap.options.label;
                                if (isProvidersLabelExist && isRightProvider) {
                                    providerFound = true;
                                    activeRequests++;
                                    self._fireSendResultRequest({ provider: ap });
                                    try {
                                        const res = ap.results(text, key, self._searchBounds(), callback, options);
                                        if (res && res.catch) {
                                            res.catch((er) => reject(er));
                                        }
                                    } catch (er) {
                                        reject(er);
                                    }
                                }
                            } else {
                                self._fireSendResultRequest({ provider: ap });
                                try {
                                    ap.results(text, key, self._searchBounds(), callback, options);
                                } catch (er) {
                                    reject(er);
                                }
                            }
                        }
                        if (provider && !providerFound) {
                            reject('Provider not found');
                        }
                    } else {
                        reject('Provider not found');
                    }
                }
            } else {
              reject('Query is too short');
            }
        });
    }

    _suggest(text) {
        const self = this;
        this._clearValues();
        this._settingsControl.closeSettings();
        this._startLoading();

        text = text.trim();

        const providers = this._getExactProviders(text);

        let activeRequests = providers.length;
        const results = [];
        const createCallback = (function (cbText, cbProvider) {
            return (function (error, suggestions) {
                let i;
                --activeRequests;
                if (suggestions) {
                    for (i = 0; i < suggestions.length; i++) {
                        suggestions[i].provider = cbProvider;
                    }
                }
                if (cbProvider._lastRender !== cbText && cbProvider.nodes) {
                    for (i = 0; i < cbProvider.nodes.length; i++) {
                        if (cbProvider.nodes[i].parentElement) {
                            self._suggestions.removeChild(cbProvider.nodes[i]);
                        }
                    }
                    cbProvider.nodes = [];
                }
                if (suggestions.length && self._input.value.trim() === cbText) {
                    if (cbProvider.nodes) {
                        for (let k = 0; k < cbProvider.nodes.length; k++) {
                            if (cbProvider.nodes[k].parentElement) {
                                self._suggestions.removeChild(cbProvider.nodes[k]);
                            }
                        }
                    }
                    self.sortSuggestions(suggestions);
                    cbProvider._lastRender = cbText;
                    cbProvider.nodes = self._renderSuggestions(suggestions);
                    const result = {
                        provider: cbProvider,
                        text: cbText,
                        suggestions,
                    };
                    results.push(result);
                    const fireOpt = ['suggest', result];
                    self.fire.apply(self, fireOpt);
                }
                if (activeRequests === 0) {
                    self._stopLoading();
                    self.fire('suggestions', { results });
                }
            }).bind(self);
        }).bind(this);

        this._pendingSuggestions = [];

        const makeSuggestForProvider = function (p) {
            self._fireSendSuggestRequest({ provider: p });
            const request = p.suggestions(text, self._searchBounds(), createCallback(text, p));
            self._pendingSuggestions.push(request);
        };

        for (let fry = 0; fry < providers.length; fry++) {
            const provider = providers[fry];
            makeSuggestForProvider(provider);
        }
    }

    sortSuggestions(suggestions) {
      const self = this;
      try {
        const center = self._map.getCenter();
        const distance = function (to) {
            if (to.latlng) {
                return center.distanceTo(to.latlng);
            } else {
                return 999999;
            }
        };
        suggestions.sort(function (a, b) {
            return distance(a) > distance(b);
        });
      } catch (e) {
        if (self.options.torus) {
          throw(e);
        } else {
          // this is widget, so ignore
        }
      }
    }

    _reverse(coords, provider?, silent?, options?) {
        const self = this;
        this._clearFeatureLayers();
        const providers = provider ? [].concat(provider) : this.options.providers;
        return new Promise((resolve, reject) => {
            const createCallback = (function (c, pp) {
                return (function (error, results) {
                    if (error) {
                        reject(error);
                    } else {
                        const opt = {
                            provider: pp,
                            coord: c,
                            text: c, // TODO: everywhere add query
                            result: results, // TODO: safe replace result to results
                            results,
                        };
                        const fireOpt = [
                            'reverse', opt,
                        ];
                        if (!silent) {
                            self.fire.apply(self, fireOpt);
                        }
                        if (results && results.length) {
                            resolve(opt);
                        } else {
                            reject('No results');
                        }
                    }
                    // this.placeReverseResult(opt);
                }).bind(self);
            }).bind(this);

            const makeReverseForProvider = function (p: CustomProvider) {
                return p.reverse(coords, self._map.getZoom(), createCallback(coords, p), options);
            };

            for (let i = 0; i < providers.length; i++) {
                const p = providers[i];
                if (p.reverse) {
                    makeReverseForProvider(p);
                }
            }
        });
    }

    _searchBounds() {
        if (this.options.useMapBounds === false) {
            return null;
        }

        if (this.options.useMapBounds === true) {
            return this._map.getBounds();
        }

        if (this.options.useMapBounds <= this._map.getZoom()) {
            return this._map.getBounds();
        }

        return null;
    }

    _renderSuggestions(suggestions) {
        let currentGroup = '';
        this._suggestions.style.display = 'block';

        // set the maxHeight of the suggestions box to
        // map height
        // - suggestions offset (distance from top of suggestions to top of control)
        // - control offset (distance from top of control to top of map)
        // - 10 (extra padding)
        const suggestionMaxHeiht = this._map.getSize().y - this._suggestions.offsetTop - this._wrapper.offsetTop - 10;
        this._suggestions.style.maxHeight = (suggestionMaxHeiht) + 'px';
        const nodes = [];
        let list;
        let header;
        for (let i = 0; i < suggestions.length; i++) {
            const suggestion = suggestions[i];
            const isHeader = !header &&
                this.options.providers.length > 1 &&
                currentGroup !== suggestion.provider.options.label;
            if (isHeader) {
                header = L.DomUtil.create('span', 'geocoder-control-header', this._suggestions);
                header.textContent = suggestion.provider.options.label;
                header.innerText = suggestion.provider.options.label;
                currentGroup = suggestion.provider.options.label;
                nodes.push(header);
            }
            if (!list) {
                list = L.DomUtil.create('ul', 'geocoder-control-list', this._suggestions);
            }

            const suggestionItem = this._createSuggestionElement(suggestion);
            if (suggestionItem) {
                list.appendChild(suggestionItem);
            }
        }
        nodes.push(list);
        return nodes;
    }

    _createSuggestionElement(suggestion) {
        const suggestionItem: IGeocoderControlSuggestion = document.createElement('li');
        suggestionItem.className = 'geocoder-control-suggestion';
        suggestionItem.name = suggestion.name;

        suggestionItem.innerHTML = '';
        suggestionItem.title = suggestion.name; // added 20150728 by SA, GK request
        suggestionItem.provider = suggestion.provider;
        if (suggestion.magicKey) {
            suggestionItem['data-magic-key'] = suggestion.magicKey;
        }

        // CONTENT + MAP
        this._createSuggestionFeature(suggestion, suggestionItem);
        if (suggestion.name) {
            let splitName = ('' + suggestion.name).split(',');
            splitName = splitName.map(function (x) {
                return x.trim();
            });
            let needle = [];
            const value = this._input.value;
            if (value) {
                needle = value.trim().split(/[ ,]+/).map(function (x) {
                    return x.trim();
                });
            }
            const generalName = suggestion.generalName != undefined ? suggestion.generalName : this.updateHaystack(splitName.splice(0, 2).join(', '), needle);
            const subName = suggestion.subName != undefined ? suggestion.subName : this.updateHaystack(splitName.join(', '), needle);
            suggestionItem.innerHTML += '<div class=\'suggestion-block-part suggestion-name-block\'>' +
                '<div class=\'suggestion-name-block__general\'>' + generalName + '</div>' +
                (subName ? '<div class=\'suggestion-name-block__subname\'>' + subName + '</div>' : '') +
                '</div>';
        }
        return suggestionItem;
    }

    updateHaystack(input, needle) {
        needle = [].concat(needle);
        needle.forEach(function (a) {
            if (a.trim()) {
                input = input.replace(new RegExp('(' + escapeRegExp(a) + ')', 'ig'), '<b>$1</b>');
            }
        });
        return input;
    }

    _createSuggestionFeature(suggestion, suggestionItem) {
        const self = this;
        try {
            if (this.options.showMapSuggestion && suggestion.latlng) {
                let directionBlock = '<div class=\'suggestion-block-part suggestion-direction-block\'>';
                const center = this._map.getCenter();
                let color;
                const geodesy = this._getGeodesy(center, suggestion.latlng);
                if (geodesy.distance) {
                    const distance = geodesy.distance / 1000;
                    const d = Math.round(100 - ((100 / 6000) * (distance || 1e-7)));
                    color = this.numberToColorRgb(d);
                    // directionBlock += "<span style='color:" + color + "'>" +
                    //     distance.toFixed(distance > 1 ? 1 : 3) + "km</span>&nbsp;&nbsp;";
                }
                if (geodesy.gzn) {
                    directionBlock += '<i class=\'fa fa-long-arrow-up\' aria-hidden=\'true\' ' +
                        'style=\'transform:rotate(' + geodesy.gzn + 'rad);' +
                        (color ? 'color:' + color + '\'' : '\')></i>&nbsp;&nbsp;');
                }

                const style = Object.assign({}, this.options.defaultSuggestionLayerStyle);
                if (color) {
                    style.color = color;
                }
                let geom;
                const featureLayers = new L.FeatureGroup();
                if (suggestion.geojson) {
                    geom = new L.GeoJSON(suggestion.geojson, { style: () => style });
                } else {
                    geom = L.circleMarker(suggestion.latlng, style);
                }

                featureLayers.addLayer(geom);

                const polyline = L.polyline([center, suggestion.latlng], style);
                featureLayers.addLayer(polyline);

                geom.name = geom.name = suggestion.name;
                suggestionItem._layers = [geom, polyline]; // Set `_layers` options inside element
                suggestionItem.innerHTML += directionBlock + '</div>';

                featureLayers.bindTooltip(geom.name, {
                    sticky: true,
                    className: 'selection-label',
                });

                const onFeatureOver = function (e) {
                    if (e.layer.setStyle) {
                        e.layer.setStyle(self.options.hoverSuggestionLayerStyle);
                    }
                };
                const onFeatureOut = function (e) {
                    if (e.layer.setStyle) {
                        e.layer.setStyle(self.options.defaultSuggestionLayerStyle);
                    }
                };
                const onFeatureClick = function (e) {
                    L.DomEvent.stop(e);
                    self._geocodeBySuggestionItem(suggestionItem);
                };

                featureLayers.on('click', onFeatureClick, this);
                featureLayers.on('mouseover', onFeatureOver, this);
                featureLayers.on('mouseout', onFeatureOut, this);
                this.featureLayers.addLayer(featureLayers);
            }
        } catch (e) {
            if (self.options.torus) {
              throw(e);
            } else {
              // this is widget, so ignore
            }
        }
    }

    numberToColorRgb(i) {
        const red = Math.floor(255 - (255 * i / 100));
        const green = Math.floor(255 * i / 200);
        return 'rgb(' + red + ',' + green + ',0)';
    }

    /**
     * return dist and direction
     * @param {Object} fromLatLng
     * @param {Object} to
     * @returns {Object}
     */
    _getGeodesy(fromLatLng, to): { distance?: number, gzn?: number } {
        const geodesy: any = {};
        geodesy.distance = fromLatLng.distanceTo(to);
        geodesy.gzn = this.calculateGzn(fromLatLng, to);
        return geodesy;
    }

    // A =PI-PI*sgn(sgn(X)+1)*sgn(Y)+arctg(Y/(X+C)).
    // sgn - (1 if x>0; 0 if x=0; -1 if x<0.)
    // C=1*10-7 to avoid division by zero.
    // X= (X2-X1);  Y=(Y2-Y1)
    calculateGzn(ex, to) {
        const c = 1e-7;
        const y1 = ex.lng;
        const x1 = ex.lat;
        const y2 = to.lng;
        const x2 = to.lat;
        const x = x2 - x1;
        const y = y2 - y1;
        const sgn = function (i) {
            let s = 0;
            if (i > 0) {
                s = 1;
            } else if (i < 0) {
                s = -1;
            }
            return s;
        };
        // RAD.  TO DEG USE * (180/Math.PI);
        return Math.PI - Math.PI * sgn(sgn(x) + 1) * sgn(y) + Math.atan(y / (x + c));
    }

    _getSuggestionName(suggestionItem) {
        return suggestionItem && suggestionItem.name;
    }

    _boundsFromResults(results) {
        results = results.filter(function (x) {
            return x.latlng || x.bounds || x.geojson;
        });
        if (!results.length) {
            return;
        }
        const nullIsland = new L.LatLngBounds([0, 0], [0, 0]);
        const resultBounds = [];
        const resultLatlngs = [];

        for (let i = results.length - 1; i >= 0; i--) {
            const result = results[i];

            let useGeoJsonBounds = false;
            if (result.geojson) {
                const geoJsonLayer: any = L.GeoJSON.geometryToLayer(result.geojson);
                if (geoJsonLayer) {
                    if ('getBounds' in geoJsonLayer) {
                        const geoJsonBounds = (geoJsonLayer as L.Polygon).getBounds();
                        if (geoJsonBounds && geoJsonBounds.isValid() && !geoJsonBounds.equals(nullIsland)) {
                            resultBounds.push(geoJsonBounds);
                            useGeoJsonBounds = true;
                        }
                    } else if ('_latlng' in geoJsonLayer) {
                        result.latlng = geoJsonLayer._latlng;
                    }
                }
            }
            // make sure bounds are valid and not 0,0. sometimes bounds are incorrect or not present
            const isBounds = !useGeoJsonBounds &&
                result.bounds &&
                result.bounds.isValid() &&
                !result.bounds.equals(nullIsland);
            if (isBounds) {
                resultBounds.push(result.bounds);
            }
            // ensure that the bounds include the results center point
            resultLatlngs.push(result.latlng);
        }

        // form a bounds object containing all center points
        const bounds = new L.LatLngBounds(resultLatlngs);

        // and extend it to contain all bounds objects
        for (let j = 0; j < resultBounds.length; j++) {
            bounds.extend(resultBounds[j]);
        }
        return bounds;
    }

    close() {
        this.clear();
        if (this.options.collapseOnCloseBtn) {
            this._collapse();
        }
        this.fire('close');
    }

    clear() {
        const self = this;
        this._settingsControl.hide();
        if (!this.options.saveInputAfterResult) {
            this._clearInput();
        } else if (this.options.clearAfterResult) {
            const onInputFocusAfterClean = function () {
                L.DomEvent.off(self._input, 'focus', onInputFocusAfterClean, self);
                const inputValue = self._input.value.trim();
                const inputValueLength = inputValue.length;
                if (inputValueLength >= this.options.minInputLength) {
                    self._suggest(inputValue);
                }
            };
            L.DomEvent.on(this._input, 'focus', onInputFocusAfterClean, this);
        }
        this._input.blur();
        this._clearValues();
        this.stopDetectInputChanges();
        if (this.options.collapseAfterResult) {
            this._collapse();
        }
        if (!this._map.scrollWheelZoom.enabled() && this._map.options.scrollWheelZoom) {
            this._map.scrollWheelZoom.enable();
        }
    }

    _clearInput() {
        this._input.value = '';
    }

    _clearValues() {
        this._clearSuggestions();
        this._abortAllRequest();
        this._stopLoading();
        this._clearFeatureLayers();
        this._mapBounds = null;
    }

    expand() {
        this._expand();
        this._input.focus();
    }

    _expand() {
        if (!this._expanded) {
            this._expanded = true;
            this._setIcoClassName();
            this._input.placeholder = this.options.placeholder;
            L.DomUtil.addClass(this._wrapper, 'geocoder-control-expanded');
            this.fire('expand');
        }
    }

    _collapse() {
        if (this._expanded) {
            this._expanded = false;
            this._clearInput();
            L.DomUtil.removeClass(this._ico, 'geocoder-control-ico-close');

            this._input.placeholder = '';
            L.DomUtil.removeClass(this._wrapper, 'geocoder-control-expanded');
            this.fire('collapse');
        }
    }

    _stopLoading() {
        L.DomUtil.removeClass(this._ico, 'geocoder-control-loading');
        this._setIcoClassName();
    }

    _startLoading() {
        L.DomUtil.removeClass(this._ico, 'geocoder-control-ico-close');
        L.DomUtil.addClass(this._ico, 'geocoder-control-loading');
    }

    _clearFeatureLayers() {
        this.featureLayers.clearLayers();

        this._returnMapFromSuggestionsView();
    }

    clearResultFeatureLayers(eventType?) {
        if (eventType && this.options.torus) {
          this.options.torus._fireClientLogEvent(eventType, { name: 'click_clearResultFeatureLayers' });
        }
        this.resultFeatureLayers.clearLayers();

        this._returnMapFromSuggestionsView();
    }

    _clearSuggestions() {
        this._suggestions.innerHTML = '';
        this._suggestions.style.display = 'none';
    }

    onAdd(map) {
        this._map = map;

        this.featureLayers.addTo(this._map);
        this.resultFeatureLayers.addTo(this._map);

        const wrapper = this._wrapper = L.DomUtil.create('div', 'geocoder-control');

        // wrapper.appendChild(this._createTools());
        if (this.options.displaySettings) {
            wrapper.appendChild(this._createSettingsIco());
        }
        wrapper.appendChild(this._createIco());
        wrapper.appendChild(this._createInput());
        if (this.options.displaySettings) {
            wrapper.appendChild(this._settingsControl.getContainer());
            this._settingsControl.addEventsListeners();
        }
        wrapper.appendChild(this._createSuggestionBlock());

        if (this.options.expanded) {
            this._expand();
        }
        if (this.options.disableClickPropagation) {
          L.DomEvent.disableClickPropagation(wrapper);
        }

        return wrapper;
    }

    // _createTools: function () {
    //     var className = "geocoder-control-input-tools";
    //     var tools = this._ico = L.DomUtil.create("div", className);
    //
    //     var journal = L.DomUtil.create("div", className + "__journal", tools);
    //     return tools;
    // },

    _createSettingsIco() {
        const ico = this._settingsControl.getControlIco();
        const setVisible = () => {
            if (this._expanded) {
                ico.style.display = 'block';
            } else {
                ico.style.display = 'none';
            }
        };
        setVisible();
        this.on('expand', setVisible, this);
        this.on('collapse', setVisible, this);
        return ico;
    }

    _setIcoClassName() {
        if (this._expanded && !this.options.alwaysExpanded) {
            L.DomUtil.addClass(this._ico, 'geocoder-control-ico-close');
        } else {
            const ico = this._ico;
            if (this._input.value) {
                L.DomUtil.addClass(ico, 'geocoder-control-ico-hover');
                ico.title = L._('Close');
            } else {
                L.DomUtil.removeClass(ico, 'geocoder-control-ico-hover');
                ico.title = this.options.title;
            }
        }
    }

    /**
     * May be search, loading or close (on hover)
     * @private
     */
    _createIco() {
        const className = 'geocoder-control-input-ico';

        const ico = this._ico = L.DomUtil.create('div', className);
        ico.title = this.options.title;
        if (this.options.disableClickPropagation) {
            L.DomEvent.disableClickPropagation(ico);
        }
        const onIcoClick = (e) => {
            // L.DomEvent.stop(e);
            if (this._expanded) {
                L.DomUtil.removeClass(ico, 'geocoder-control-ico-hover');
                this.close();
                if (this.options.alwaysExpanded) {
                    this._clearInput();
                }
                if (this.options.collapseOnCloseBtn) {
                    this._collapse();
                }
                this.clearResultFeatureLayers();
                this.fire('close-ico-click');
            } else {
                this.expand();
            }
        };

        L.DomEvent.on(ico, 'click', onIcoClick, this);

        // const onMouseOver = function () {
        //     if (self._expanded) {
        //         L.DomUtil.addClass(ico, "geocoder-control-ico-hover");
        //         ico.title = L._("Close");
        //     }
        // };

        // const onMouseOut = function () {
        //     L.DomUtil.removeClass(ico, "geocoder-control-ico-hover");
        //     ico.title = self.options.title;
        // };

        L.DomEvent.on(ico, 'mouseover', this._setIcoClassName, this);
        L.DomEvent.on(ico, 'mouseout', this._setIcoClassName, this);
        return ico;
    }

    _createInput() {
        const self = this;
        const input = this._input = L.DomUtil.create('input', 'geocoder-control-input leaflet-bar') as HTMLInputElement;

        L.DomEvent.on(this._input, 'contextmenu', function (e) {
            if (this.options.disableClickPropagation) {
                L.DomEvent.stopPropagation(e);
            }
        }, this);

        L.DomEvent.addListener(this._input, 'keydown', this._onInputKeydown, this);
        L.DomEvent.addListener(this._input, 'keyup',
            this.throttle(this._onInputKeyup, this.options.inputDelay, this), this);

        if (this.options.clearOnBlur) {
            L.DomEvent.addListener(this._input, 'blur', this.clear, this);
        }
        L.DomEvent.addListener(this._input, 'focus', function () {
            self._expand();
            self.detectInputChanges();
        }, this);

        this._input.title = this.options.title;

        return input;
    }

    _geocodeBySuggestionItem(suggestionItem) {
        const text = this._getSuggestionName(suggestionItem);
        if (text && suggestionItem.provider) {
            this._geocode(text, suggestionItem['data-magic-key'], suggestionItem.provider);
        }
    }

    _createSuggestionBlock() {
        const self = this;

        const suggestions = this._suggestions = L.DomUtil.create('div', 'geocoder-control-suggestions leaflet-bar');

        L.DomEvent.addListener(suggestions, 'mousedown', function (e) {
            L.DomEvent.stop(e);
            let suggestionItem: Element = (e.target || e.srcElement) as Element;
            suggestionItem = suggestionItem.closest('.geocoder-control-suggestion');
            self._geocodeBySuggestionItem(suggestionItem);
        }, this);

        L.DomEvent.addListener(suggestions, 'mousedown', function () {
            L.DomEvent.removeListener(self._input, 'blur', self.clear, self);
        }, self);

        // when mouse moves leaves suggestions enable scroll wheel zoom if its disabled
        L.DomEvent.addListener(suggestions, 'mouseout', this._onSuggestionMouseOut, this);

        // when mouse moves over suggestions disable scroll wheel zoom if its enabled
        L.DomEvent.addListener(suggestions, 'mouseover', this._onSuggestionMouseOver, this);

        return suggestions;
    }

    detectInputChanges() {
        let initialValue = this._input.value.trim();
        this.stopDetectInputChanges();
        this._changeIntervalId = window.setInterval(() => {
            const changeValue = this._input.value.trim();
            const changeLength = changeValue.length;
            this._setIcoClassName();
            if (initialValue !== changeValue) {
                initialValue = changeValue;
                if (changeLength >= this.options.minInputLength) {
                    this.throttle(this._suggest, this.options.inputDelay, this)(this._input.value);
                }
            }
        }, 100);
    }

    stopDetectInputChanges() {
        if (this._changeIntervalId) {
            clearInterval(this._changeIntervalId);
            this._changeIntervalId = null;
        }
    }

    _onInputKeyup(e) {
        // var key = e.which || e.keyCode;
        const text = (e.target || e.srcElement).value.trim();

        // require at least `minInputLength` characters for suggestions
        if (text.length < this.options.minInputLength) {
            this._clearValues();
        }
    }

    throttle(fn, delay, context) {
        return function (...args: any[]) {
            if (fn._executeTimeout) {
                clearTimeout(fn._executeTimeout);
            }
            fn._executeTimeout = setTimeout(function () {
                fn.apply(context, args);
            }, delay);
        };
    }

    _getSuggestionFromTarget(target) {
        while (target) {
            if (target.provider) {
                return target;
            } else {
                target = target.parentNode;
            }
        }
        return false;
    }

    _onSuggestionMouseOver(e) {
        const map = this._map;
        if (map.scrollWheelZoom.enabled() && map.options.scrollWheelZoom) {
            map.scrollWheelZoom.disable();
        }
        L.DomEvent.off(this._input, 'blur', this.clear);
        const suggestionItem = this._getSuggestionFromTarget(e.target || e.srcElement);

        if (suggestionItem) {
            const text = this._getSuggestionName(suggestionItem);
            if (suggestionItem.provider._results) {
                if (this.options.autoZoom) {
                    this._mapBounds = this._map.getBounds();
                    for (let fry = 0; fry < suggestionItem.provider._results.length; fry++) {
                        const result = suggestionItem.provider._results[fry];
                        if (result.name === text) {
                            if (result.bounds) {
                                map.fitBounds(result.bounds, { padding: [50, 50], animate: false });
                                //   this.createPolygonFromBounds(result.bounds, this._map).addTo(this.featureLayers)
                            } else if (result.latlng) {
                                const mapZoom = map.getZoom();
                                map.setView(result.latlng, (mapZoom < 13 ? mapZoom : 13), { animate: false });
                            }
                            break;
                        }
                    }
                }
            }
            this.setElementLayersStyle(suggestionItem, this.options.hoverSuggestionLayerStyle);
        }
    }

    // Just for test, safe REMOVE
    createPolygonFromBounds(latLngBounds, map) {
        const center = latLngBounds.getCenter();
        const latlngs = [];

        latlngs.push(latLngBounds.getSouthWest()); // bottom left
        latlngs.push({ lat: latLngBounds.getSouth(), lng: center.lng }); // bottom center
        latlngs.push(latLngBounds.getSouthEast()); // bottom right
        latlngs.push({ lat: center.lat, lng: latLngBounds.getEast() }); // center right
        latlngs.push(latLngBounds.getNorthEast()); // top right
        latlngs.push({ lat: latLngBounds.getNorth(), lng: map.getCenter().lng }); // top center
        latlngs.push(latLngBounds.getNorthWest()); // top left
        latlngs.push({ lat: map.getCenter().lat, lng: latLngBounds.getWest() }); // center left

        return L.polygon(latlngs);
    }

    _onSuggestionMouseOut(e) {
        const suggestionItem = this._getSuggestionFromTarget(e.target || e.srcElement);
        this._returnMapFromSuggestionsView();
        if (suggestionItem) {
            this.setElementLayersStyle(suggestionItem, this.options.defaultSuggestionLayerStyle);
        }
    }

    setDefaultStyleForAllSuggestionLayer() {
        const style = this.options.defaultSuggestionLayerStyle;
        this.featureLayers.eachLayer(function (x: L.Path) {
            x.setStyle(style);
        });
    }

    setElementLayersStyle(element, style) {
        const layers = element && element._layers;
        if (layers) {
            for (let fry = 0; fry < layers.length; fry++) {
                const layer = layers[fry];
                layer.setStyle(style);
            }
        }
    }

    _returnMapFromSuggestionsView() {
        const map = this._map;
        if (!map.scrollWheelZoom.enabled() && map.options.scrollWheelZoom) {
            map.scrollWheelZoom.enable();
        }
        if (this.options.clearOnBlur) {
            L.DomEvent.addListener(this._input, 'blur', this.clear, this);
        }

        if (this._mapBounds) {
            map.fitBounds(this._mapBounds, { animate: false });
        }
        this._mapBounds = null;
        // this.featureLayers.clearLayers();
    }

    _onInputKeydown(e) {
        const self = this;
        const key = e.which || e.keyCode;
        // if this is the escape key it will clear the input so clear suggestions
        if (key === 27) {
            self.close();
            return;
        }

        this._expand();
        const list = self._suggestions.querySelectorAll('.' + 'geocoder-control-suggestion');
        const firsInList = list[0] as IGeocoderControlSuggestion;
        const lastInList = list[list.length - 1] as IGeocoderControlSuggestion;
        const selected = self._suggestions.querySelectorAll('.' + 'geocoder-control-selected')[0] as IGeocoderControlSuggestion;
        let selectedPosition;

        for (let i = 0; i < list.length; i++) {
            if (list[i] === selected) {
                selectedPosition = i;
                break;
            }
        }

        switch (key) {
            case 13: // Enter
                if (selected) {
                    self._geocode(self._getSuggestionName(selected), selected['data-magic-key'], selected.provider);
                } else if (this.options.allowMultipleResults) {
                    self._geocode(self._input.value, undefined);
                } else if (list.length) { // Geocode first result
                    self._geocode(self._getSuggestionName(list[0]), null, firsInList.provider);
                    L.DomUtil.addClass(firsInList, 'geocoder-control-selected');
                } else {
                    const providers = self.options.providers.slice();
                    // find any result in each registered provider synchronous
                    loop<CustomProvider>(providers, (provider, idx) => {
                        return self._geocode(this._input.value, null, provider).then((res) => {
                            if (res && res.name) {
                                // stop find result on firs value
                                return false;
                            }
                        }).catch(() => {
                            // ignore
                        });
                    });
                }
                L.DomEvent.preventDefault(e);
                break;
            case 38: // up arrow
                if (selected) {
                    L.DomUtil.removeClass(selected, 'geocoder-control-selected');
                }

                const previousItem = list[selectedPosition - 1] as IGeocoderControlSuggestion;

                if (selected && previousItem) {
                    L.DomUtil.addClass(previousItem, 'geocoder-control-selected');
                } else {
                    L.DomUtil.addClass(lastInList, 'geocoder-control-selected');
                }
                L.DomEvent.preventDefault(e);
                break;
            case 40: // down arrow
                if (selected) {
                    L.DomUtil.removeClass(selected, 'geocoder-control-selected');
                }

                const nextItem = list[selectedPosition + 1] as IGeocoderControlSuggestion;

                if (selected && nextItem) {
                    L.DomUtil.addClass(nextItem, 'geocoder-control-selected');
                } else {
                    L.DomUtil.addClass(firsInList, 'geocoder-control-selected');
                }
                L.DomEvent.preventDefault(e);
                break;
            default:
                this._abortAllRequest();
                break;
        }
    }

    _abortAllRequest() {
        // when the input changes we should cancel all pending suggestion requests
        // if possible to avoid result collisions
        for (let x = 0; x < this._pendingSuggestions.length; x++) {
            const request = this._pendingSuggestions[x];
            // TODO: make possible for custom providers
            if (request) {
                if (request && request.abort && !request.id) {
                    request.abort();
                    // work around an Esri Leaflet bug. Remove for 1.0.0/RC.2
                } else if (request.id && window._EsriLeafletCallbacks[request.id].abort) {
                    window._EsriLeafletCallbacks[request.id].abort();
                }
            }
        }
    }

    onRemove(map) {
        if (map.attributionControl) {
            map.attributionControl.removeAttribution('Geocoding by Esri');
        }
        this.clearResultFeatureLayers();
        this.clear();
    }

    // // RESULT POPUP

    placeReverseResult(opt, elements?) {
        this.getPopup(opt, elements);
    }

    findNearest(results, coord) {
        let dist = null;
        const tmpRes = results[0];
        let res = tmpRes;
        if (coord) {
            for (let fry = 0; fry < results.length; fry++) {
                if (tmpRes.latlng) {
                    const tmpDist = coord.distanceTo(tmpRes.latlng);
                    if (!dist || tmpDist < dist) {
                        dist = tmpDist;
                        res = tmpRes;
                    }
                }
            }
        }
        return res;
    }

    getPopup(opt, elements?) {
        let fry;
        // var str = "";
        if (opt && opt.result && opt.result.length) {

            const res = this.findNearest(opt.result, opt.coord);

            if (res && (res.latlng || res.name)) {
                let geom;
                if (res.geojson) {
                    geom = new L.GeoJSON(res.geojson, {
                        style: () => Object.assign({}, this.options.defaultSuggestionLayerStyle),
                    });
                } else {
                    geom = L.marker(res.latlng).addTo(this.resultFeatureLayers);
                }
                geom.addTo(this.resultFeatureLayers);

                const popupContainer = document.createElement('div');
                const infoElement = document.createElement('div');

                if (opt.provider && opt.provider.options && opt.provider.options.label) {
                    const header = document.createElement('div');
                    header.innerHTML = '<b>' + opt.provider.options.label + ':</b>';
                    infoElement.appendChild(header);
                }
                if (res.name) {
                    infoElement.appendChild(this._createCopyElementBtn(res.name));
                }
                infoElement.appendChild(this._createCopyElementBtn(
                    `<b class='no-copy'>${L._('Lat/Lng')}: </b>${res.latlng.lat.toFixed(5)} ${res.latlng.lng.toFixed(5)}`,
                ));
                if (elements) {
                    elements.info.forEach((e) => infoElement.appendChild(this._crateElementBtn(e, elements.buttons[e.code])));
                }

                popupContainer.appendChild(infoElement);

                const controlElement = document.createElement('div');
                controlElement.className = 'reverse-popup-control';

                let actions = [
                    {
                        name: '<i class=\'fa fa-trash-o\'></i> ' + L._('Remove all results'),
                        action: this.clearResultFeatureLayers.bind(this, 'map-event'),
                    },
                    // {
                    //    name: '<i class="fa fa-files-o" aria-hidden="true"></i> ' + L._("Copy"),
                    //    action: function () { self._copyResult(controlElement, str); }
                    // }
                ];
                if (opt.customActions && opt.customActions.length) {
                    actions = actions.concat(opt.customActions);
                }
                for (fry = 0; fry < actions.length; fry++) {
                    const action = actions[fry];
                    if (action.name && action.action) {
                        controlElement.appendChild(this._createPopupControlAction(action.name, action.action));
                    }
                }

                popupContainer.appendChild(controlElement);
                const center = (geom.getLatLng && geom.getLatLng()) || geom.getBounds().getCenter();
                geom.bindPopup(popupContainer, { minWidth: this.options.popupMinWidth }).openPopup(center);

                const style = Object.assign({}, this.options.hoverSuggestionLayerStyle);

                if (opt.coord) {
                    L.polyline([res.latlng, opt.coord], style)
                        .bindPopup(popupContainer, { minWidth: this.options.popupMinWidth })
                        .addTo(this.resultFeatureLayers);
                }
            }
        }
    }

    _crateElementBtn(a, button) {
        const str = `<b class='no-copy'>${a.name}: </b>${a.value}`;
        const block = document.createElement('div');
        const text = document.createElement('span');
        text.innerHTML = str;
        block.appendChild(text);
        if (button) {
            const btn = document.createElement('a');
            btn.className = 'btn btn-outline-secondary popup-control-btn clone';
            btn.title = button.title;
            btn.innerHTML = `<i class=\'fa ${button.icon}\'></i>`;
            block.appendChild(btn);
            btn.onclick = button.action.bind(this, a);
        }
        return block;
    }

    _createCopyElementBtn(str) {
        const self = this;
        const block = document.createElement('div');
        const text = document.createElement('span');
        text.innerHTML = str;
        block.appendChild(text);
        const btn = document.createElement('a');
        btn.className = 'btn btn-outline-secondary popup-control-btn clone';
        btn.title = L._('Copy');
        btn.innerHTML = '<i class=\'fa fa-files-o\'></i>';
        block.appendChild(btn);
        btn.onclick = function () {
            self._copyResult(text, str);
        };
        return block;
    }

    _createPopupControlAction(html, onClick) {
        const span = document.createElement('span');
        const btn = document.createElement('a');
        btn.className = 'btn btn-outline-secondary reverse-popup-control-btn'; // TODO: rename without reverse
        btn.innerHTML = html;
        btn.onclick = onClick;
        span.appendChild(btn);
        return span;
    }

    _copyResult(fromBlock, str) {
        const resultBlockContainer = fromBlock;
        const textinput = document.createElement('textarea');
        resultBlockContainer.appendChild(textinput);

        const tmp = document.createElement('DIV');
        tmp.innerHTML = str;
        const noCopy = tmp.getElementsByClassName('no-copy');
        if (noCopy.length) {
            for (let fry = noCopy.length; fry--;) {
                const nc = noCopy[fry];
                nc.parentNode.removeChild(nc);
            }
        }
        tmp.innerHTML = tmp.innerHTML.replace(/<br\s*\/?>/mg, '\n');

        textinput.value = tmp.textContent || tmp.innerText || '';
        L.DomUtil.removeClass(resultBlockContainer, 'hidden');
        textinput.select();
        try {
            const successful = document.execCommand('copy');
            const msg = successful ? 'successful' : 'unsuccessful';
            console.log('Copying text command was ' + msg);
        } catch (err) {
            console.log('Oops, unable to copy');
        }
        resultBlockContainer.removeChild(textinput);
    }

    // // FIRE EVENTS

    _fireSendResultRequest(data) {
        this.fire('result:request', { provider: data.provider });
    }

    _fireSendSuggestRequest(data) {
        this.fire('suggest:request', { provider: data.provider });
    }
}

EsriLeafletGeocoding.Controls.geosearch = function (options) {
    return new Geosearch(options);
};

EsriLeafletGeocoding.Controls.Geosearch = Geosearch;
