import L from 'leaflet';
import '../../Geosearch';
import { CustomProvider, Geocoder } from '../CustomProvider';
import { CustomGeocoder } from '../CustomService';

export class NominatimGeocoder extends CustomGeocoder {
    static options = {
        'serviceUrl': '//nominatim.openstreetmap.org/',
        'geocodingQueryParams': {},
        'reverseQueryParams': {},
        'accept-language': 'ru',
    };

    geocode(query, bounds, cb, context) {
        const options: any = Object.assign(this.options.geocodingQueryParams, {
            q: query,
            limit: 5,
            format: 'json',
            addressdetails: 1,
            polygon_geojson: 1,
        });
        if (bounds) {
            // options.viewbox = bounds.toBBoxString();
            options.viewbox = [bounds.getWest(), bounds.getSouth(), bounds.getEast(), bounds.getNorth()].join(',');
            // options.viewboxlbrt = bounds.toBBoxString();
        }
        return Geocoder.getJSON(this.options.serviceUrl + 'search/', options,
            function (data) {
                const results = [];
                for (let i = data.length - 1; i >= 0; i--) {
                    const d = data[i];
                    const bbox = d.boundingbox;
                    for (let j = 0; j < 4; j++) {
                        bbox[j] = parseFloat(bbox[j]);
                    }
                    results[i] = {
                        name: d.display_name,
                        bounds: L.latLngBounds([bbox[0], bbox[2]], [bbox[1], bbox[3]]),
                        latlng: L.latLng(d.lat, d.lon),
                        properties: d,
                        geojson: d.geojson,
                    };
                }
                cb.call(context, results);
            }, this, 'json_callback');
    }

    reverse(location, scale, cb, context) {
        Geocoder.getJSON(this.options.serviceUrl + 'reverse/', Object.assign({
            lat: location.lat,
            lon: location.lng,
            zoom: Math.round(Math.log(scale / 256) / Math.log(2)),
            addressdetails: 1,
            format: 'json',
            polygon_geojson: 1,

        }, this.options.reverseQueryParams), function (data) {
            const result = [];
            let loc;

            if (data && data.lat && data.lon) {
                loc = L.latLng(data.lat, data.lon);
                result.push({
                    name: data.display_name,
                    latlng: loc,
                    bounds: L.latLngBounds(loc, loc),
                    properties: data,
                    geojson: data.geojson,
                });
            }

            cb.call(context, result);
        }, this, 'json_callback');
    }
}

Geocoder.Nominatim = NominatimGeocoder;

export class NominatimProvider extends CustomProvider {
    static options = {
        label: 'OSM',
        Geocoder: NominatimGeocoder,
    };
}
