import L from 'leaflet';

// Returns true if it is a DOM element
function isElement(o) {
    return (
        typeof HTMLElement === 'object' ? o instanceof HTMLElement : // DOM2
            o && typeof o === 'object' && o !== null && o.nodeType === 1 && typeof o.nodeName === 'string'
    );
}

export function htmlTemplate(str: string, props?: {[propName: string]: any}): HTMLElement {
    const temps = {};
    str = str.trim();
    for (const p in props) {
        if (props.hasOwnProperty(p)) {
            if (isElement(props[p])) {
                const temp = { element: props[p] };
                const id = L.Util.stamp(temp);
                temps[p] = temp;
                const open = '<div id=\'tmp-html-' + id + '\'>';
                const close = '</div>';
                const closeTagTemplate = '{/' + p + '}';
                if (str.indexOf(closeTagTemplate) >= 0) {
                    const contentTagTemplate = '{:' + p + '}';
                    if (str.indexOf(contentTagTemplate) >= 0) {
                        const contentTemp: {element?: HTMLElement | DocumentFragment} = {};
                        const contentTempId = L.Util.stamp(contentTemp);
                        contentTemp.element = document.createDocumentFragment();
                        while (temp.element.childNodes.length > 0) {
                            contentTemp.element.appendChild(temp.element.childNodes[0]);
                        }
                        temps['content-' + p] = contentTemp;
                        props['content-' + p] = '<div id=\'tmp-html-' + contentTempId + '\'></div>';
                        str = str.replace(contentTagTemplate, '{content-' + p + '}');
                    }
                    props[p] = open;
                    props['close-tag-' + p] = close;
                    str = str.replace(closeTagTemplate, '{close-tag-' + p + '}');
                } else {
                    props[p] = open + close;
                }
            }
        }
    }
    str = L.Util.template(str, props);
    const element = document.createElement('div') as HTMLElement;
    element.innerHTML = str;
    for (const t in temps) {
        if (temps.hasOwnProperty(t)) {
            const temp = temps[t];
            const refChild = element.querySelector('#tmp-html-' + L.Util.stamp(temp));
            while (refChild.childNodes.length > 0) {
                temp.element.appendChild(refChild.childNodes[0]);
            }
            refChild.parentNode.replaceChild(temp.element, refChild);
        }
    }
    return element.childNodes.length > 1 ? element : element.firstChild as HTMLElement;
}
