import L from 'leaflet';
import { Evented } from './Evented';

export class MapControl<O extends L.ControlOptions = any> extends Evented<O> {

    static options = {
        position: 'topright',
    };

    _container: HTMLElement;
    _map: L.Map;

    constructor(options) {
        super();
        L.Util.setOptions(this, options);
    }

    onAdd?(map: L.Map): HTMLElement;
    onRemove?(map: L.Map): void;

    getPosition() {
        return this.options.position;
    }

    // Sets the position of the control.
    setPosition(position) {
        const map = this._map;

        if (map) {
            map.removeControl(this);
        }

        this.options.position = position;

        if (map) {
            map.addControl(this);
        }

        return this;
    }

    // @method getContainer: HTMLElement
    // Returns the HTMLElement that contains the control.
    getContainer() {
        return this._container;
    }

    // @method addTo(map: Map): this
    // Adds the control to the given map.
    addTo(map) {
        this.remove();
        this._map = map;

        const container = this._container = this.onAdd(map);
        const pos: any = this.getPosition();
        const corner = map._controlCorners[pos];

        L.DomUtil.addClass(container, 'leaflet-control');

        if (pos.indexOf('bottom') !== -1) {
            corner.insertBefore(container, corner.firstChild);
        } else if (pos === 'left' || pos === 'right') {
            const controlContainer = map._controlContainer;
            controlContainer.insertBefore(container, controlContainer.firstChild);
        } else {
            corner.appendChild(container);
        }

        return this;
    }

    // @method remove: this
    // Removes the control from the map it is currently active on.
    remove() {
        if (!this._map) {
            return this;
        }

        L.DomUtil.remove(this._container);

        if (this.onRemove) {
            this.onRemove(this._map);
        }

        this._map = null;

        return this;
    }

    _refocusOnMap(e) {
        // if map exists and event is not a keyboard event
        if (this._map && e && e.screenX > 0 && e.screenY > 0) {
            this._map.getContainer().focus();
        }
    }
}
