export function createGetSrcPromise(url: string): Promise<void> {
  return new Promise(function (res, reject) {
    javaScriptLoad(url, res, reject);
  });
}

export type TProvider = string | [string, { [providerOption: string]: any }];

export function getProvider<P extends any = any>(
  p: TProvider | P,
  nameSpace: { [providerName: string]: new (...args: any[]) => P },
  options: any = {}): P {
  let Provider;
  if (typeof p === 'string') {
    Provider = nameSpace[p as string];
    if (Provider) {
      return new Provider(options);
    }
  } else if (Array.isArray(p)) {
    Provider = nameSpace[p[0]];
    if (Provider) {
      return new Provider(Object.assign({}, options, p[1]));
    }
  } else {
    // TODO: check if provider is instance of Provider
    return p;
  }
}

export function javaScriptLoad(src, resolve, reject) {
  const script = document.createElement('script');
  script.setAttribute('src', src);
  if (resolve) {
    // @ts-ignore
    script.onreadystatechange = script.onload = function () {
      resolve();
    };
    script.onerror = function (er) {
      console.log('cannot load script from ' + src);
      reject(er);
    };
  }
  document.getElementsByTagName('head')[0].appendChild(script);
}

export function loop<T = Promise<any> | any>(
  arr: T[],
  fn: (arrayItem: T, index: number, array: T[]) => Promise<any>,
  busy?, err?, i = 0): Promise<any> {
  const body = (ok, er) => {
    try {
      fn(arr[i], i, arr).then((ans) => {
        if (ans === -1) {
          i -= 2;
        }
        if (typeof ans === 'boolean' && !ans) {
          i = arr.length;
        }
        ok(ans);
      }).catch(er);
    } catch (e) {
      er(e);
    }
  };
  const next = (ok, er) => () => loop(arr, fn, ok, er, ++i);
  const run = (ok, er) => i < arr.length ? new Promise(body).then(next(ok, er)).catch(er) : ok();
  return busy ? run(busy, err) : new Promise(run);
}
