import L from 'leaflet';
import sexagesimal from 'sexagesimal';
import '../../Geosearch';
import { CustomProvider, IGeocodeResult, IGeocoderProviderOptions } from '../CustomProvider';
import { CustomGeocoder } from '../CustomService';
import '../Providers/NominatimProvider';

interface CoordinateProviderOptions extends IGeocoderProviderOptions {
    separatorChr?: string;

}

export class CoordinateService extends CustomGeocoder {
    options = {};

    geocode(query: string, bounds, cb: (results: IGeocodeResult) => any, context: any, magicKey?: any) {
        // ignore
    }
}

export class Coordinate extends CustomProvider<CoordinateProviderOptions> {

    static options = {
        label: 'Coordinates',
        separatorChr: '::',
        Geocoder: CoordinateService,
    };

    static reDd = /^[-+]?([1-8]?\d(\.\d+)?|90(\.0+)?)[, ]\s*[-+]?(180(\.0+)?|((1[0-7]\d)|([1-9]?\d))(\.\d+)?)$/;
    static reGgMmSs = /^(-?(90[ :°d]*00[ :\'\'m]*00(\.0+)?|[0-8][0-9][ :°d]*[0-5][0-9][ :\'\'m]*[0-5][0-9](\.\d+)?)[ :\?\"s]*(N|n|S|s|[с,ю]\.?ш\.?)?)[ ,]*(-?(180[ :°d]*00[ :\'\'m]*00(\.0+)?|([1]?[0-7][1-9]|[1]?[1-8][0]|[1-9]?[0-9])[ :°d]*[0-5][0-9][ :\'\'m]*[0-5][0-9](\.\d+)?)[ :\?\"s]*(E|e|W|w|[з,в]\.?д\.?)?)$/;
    static execDg(text) {
        return Coordinate.reDd.exec(text);
    }
    static execGgMmSs(text) {
        return Coordinate.reGgMmSs.exec(text);
    }

    isQueryExactlyForThisProvider(text) {
        return !!this._textToCoordinate(text); // Coordinate.execDg(text) || Coordinate.execGgMmSs(text);
    }

    _toCoordinate(text) {
        const pair = sexagesimal.pair(text);
        if (pair && pair.length === 2) {
            return L.latLng(pair[0].toFixed(5), pair[1].toFixed(5));
        }
        return false;
    }

    _textToCoordinate(text) {
        let x;
        let y;
        let coord = Coordinate.execDg(text);
        if (coord) {
            x = coord[1];
            y = coord[4];
        } else {
            coord = Coordinate.execGgMmSs(text);
            if (coord) {
                const lat: number[] = coord[2].split(' ').map((z) => Number(z));
                const lng: number[] = coord[7].split(' ').map((z) => Number(z));
                x = lat[0] * 1 + (lat[1] / 60) + (lat[2] / 3600);
                y = lng[0] * 1 + (lng[1] / 60) + (lng[2] / 3600);
            }
        }
        if (x && y) {
            return L.latLng(x, y);
        }
        return this._toCoordinate(text);
    }

    suggestions(text, bounds, callback) {
        const latlng = this._textToCoordinate(text);
        const simpleCallback = function (_latlng) {
            const suggestions = [{ name: _latlng.lat + ' ' + _latlng.lng, latlng: _latlng }];
            callback('', suggestions);
        };
        if (latlng) {
            simpleCallback(latlng);
        } else {
            callback('', []);
        }
    }

    results(text, key, bounds, callback) {
        const result: IGeocodeResult = {};
        text = text.split(this.options.separatorChr)[0];
        const latlng = this._textToCoordinate(text);
        if (latlng) {
            result.latlng = latlng;
            result.text = text;

        }
        callback('', result);
    }

    // reverse: function (location, scale, cb, context) {
    //     const result = [];
    //     if (location && location.lat && location.lng) {
    //         result.push({
    //             additional: location,
    //             latlng: location,
    //         });
    //     }
    //     cb.call(context, "", result);
    //
    // }
}
