import './TorusGeocoder.css';

import L, { Map, Evented, MapOptions, tileLayer, FeatureGroup, LeafletMouseEvent, CircleMarker, LeafletEvent, LatLng } from 'leaflet';
import 'leaflet-plugins/layer/tile/Yandex';
import { Geocoder } from './Geocoder';

import { Coordinate } from '../../geocod-ex/src/js/Geocoder/Providers/CoordinateProvider';
import { GoogleProvider } from '../../geocod-ex/src/js/Geocoder/Providers/GoogleProvider';
import { NominatimProvider } from '../../geocod-ex/src/js/Geocoder/Providers/NominatimProvider';
import { OpenStreetMapRuProvider } from '../../geocod-ex/src/js/Geocoder/Providers/OpenStreetMapRu';
import { GwiYandexProvider } from '../../../extensions/gwi/geocoder/GwiYandexProvider';
import { YandexProvider } from '../../geocod-ex/src/js/Geocoder/Providers/YandexProvider';
import { createGetSrcPromise, getProvider } from '../../torus-map/TrotilExt';
import { post } from '../../../extensions/gwi/geocoder/utils';
import { IGeosearchOptions } from '../../geocod-ex/src/js/Geosearch';
// import { LBSProvider } from '../../geocod-ex/src/js/Geocoder/Providers/LBSProvider';
// import { FakeProvider } from '../../geocod-ex/src/js/Geocoder/Providers/FakeProvider';
// import { ArcgisOnlineGeocoder } from '../../geocod-ex/src/js/Geocoder/Providers/ArcgisOnlineGeocoder';
// import { BingProvider } from '../../geocod-ex/src/js/Geocoder/Providers/BingProvider';

export interface TileLayerOptions {
  urlTemplate: string;
  attribution?: string;
  subdomains?: string | Array<string>;
}

export interface TorusGeocoderOptions extends IGeosearchOptions {
  id: string;
  defaultBaseMap?: string;
  mapOptions?: MapOptions;
  tileLayer?: TileLayerOptions;
}

// Geocoder.Providers.ArcGISOnline = ArcgisOnlineGeocoder;
// Geocoder.Providers.Bing = BingProvider;
Geocoder.Providers.Coordinate = Coordinate;
// Geocoder.Providers.Fake = FakeProvider;
Geocoder.Providers.Google = GoogleProvider;
Geocoder.Providers.Nominatim = NominatimProvider;
Geocoder.Providers.OpenStreetMapRu = OpenStreetMapRuProvider;
Geocoder.Providers.Yandex = YandexProvider;
Geocoder.Providers.GwiYandex = GwiYandexProvider;
// Geocoder.Providers.LBSProvider = LBSProvider;

const MAP_OPTIONS: MapOptions = {
  center: [55.752833, 37.620405],
  zoom: 12
}

export class TorusGeocoder extends Evented {
  options: Partial<TorusGeocoderOptions> = {
    // defaultBaseMap: 'osm',
    placeholder: "Поиск по адресу",
    alwaysExpanded: true,
    collapseAfterResult: true,
    clearAfterResult: true,
    saveInputAfterResult: true,
    useSuggestTextToSaveInput: true,
    useMapBounds: false,
    displaySettings: false,
    providers: [],
    showMapSuggestion: false,
    disableClickPropagation: false,
    isWiget: true
  };
  map?: Map;
  searchControl?: Geocoder;
  mapToggleStatus = false;
  private _mapContainer?: HTMLElement;
  private _inputContainer?: HTMLInputElement;
  private _mapClickLayer?: FeatureGroup;
  private _defaultMarkerZoomLevel = 16;

  constructor(options: TorusGeocoderOptions) {
    super();
    this.options.id = options.id;
    if (options.tileLayer) {
      this.options.tileLayer = options.tileLayer;
    }
    if (options.displaySettings) {
      this.options.displaySettings = options.displaySettings;
    }
    if (options.placeholder) {
      this.options.placeholder = options.placeholder;
    }
    if (options.providers) {
      this.options.providers = options.providers;
    } else {
      throw new Error('No providers');
    }

    this._createContainers(this.options.id);

    this._buildMap();

    this._buildControl(this.options);
    this._replaceInput();
    this._addEventsListener();
  }

  toggleMap() {
    if (this.mapToggleStatus) {
      this.hideMap()
    } else {
      this.showMap();
    }
  }

  hideMap() {
    if (this._mapContainer) {
      this._mapContainer.style.display = 'none';
    }
    this.mapToggleStatus = false;
    if (this.map) {
      this._removeMapListeners();
    }
  }

  showMap() {
    const map = this.map;
    if (this._mapContainer) {
      this._mapContainer.style.display = 'block';
    }

    if (map) {
      const zoomLevel = map.getZoom();

      map.invalidateSize();
      const clickLayers = this._mapClickLayer
      if (!zoomLevel && clickLayers) {
        if (clickLayers.getLayers().length) {
          map.setView(clickLayers.getBounds().getCenter(), this._defaultMarkerZoomLevel);
        }
      }
      this._addMapListeners();
    }
    this.mapToggleStatus = true;
  }

  clean() {
    this._clearMapClickLayer();
  }

  private _buildControl(options: any) {
    if (options.providers) {
      const newProviders = [];
      for (let fry = 0; fry < options.providers.length; fry++) {
        const p = options.providers[fry];
        const isNotEnabledProvider = typeof p === 'object' &&
          typeof p[1] === 'object' &&
          typeof p[1].enabled !== 'undefined' &&
          !p[1].enabled;
        if (isNotEnabledProvider) continue;
        const provider = getProvider(p, Geocoder.Providers, { torus: this });
        if (provider) {
          newProviders.push(provider);
        }
      }
      options.providers = newProviders;
    }

    this.searchControl = new Geocoder(options)
    // this._addEventsListeners();
    if (this.map) {
      this.searchControl.addTo(this.map);
    }

    return this.searchControl;
  }

  private _replaceInput() {
    const navSearchInput = this._inputContainer;
    const geocoder = this.searchControl;
    if (geocoder && navSearchInput) {
      const geocoderInput = geocoder.getInput();
      const gorcoderContainer = geocoderInput.parentNode;
      if (navSearchInput.parentNode && gorcoderContainer) {
        navSearchInput.parentNode.replaceChild(gorcoderContainer, navSearchInput);
      }

    }
  }

  private _createContainers(toId: string) {
    const elem = document.getElementById(toId);
    this._inputContainer = document.createElement('input');
    this._mapContainer = document.createElement('div');
    this._mapContainer.className = 'torus-geocoder-map';
    if (elem) {
      elem.appendChild(this._inputContainer);
      elem.appendChild(this._mapContainer);
    }
    this.hideMap();
  }

  private _buildMap() {
    const self = this;
    if (this._mapContainer) {
      this.map = new Map(this._mapContainer, { ...MAP_OPTIONS });
      const tileLayerOpt = this.options.tileLayer;
      if (tileLayerOpt && tileLayerOpt.layerType) {
        if (tileLayerOpt.layerType == 'Yandex') {
            // get Yandex API URL
            post(tileLayerOpt.getUrlYandex, { withCredentials: true, format: 'json', method: 'POST' }).then((apiSourceUrl: string) => {
              if (apiSourceUrl && typeof apiSourceUrl === 'string') {
                // embed Yandex API script in DOM
                createGetSrcPromise(apiSourceUrl)
                  .then(() => {
                    window.ymaps.ready((data) => {
                      const yandexLayer = new L.Yandex('yandex#map');
                      yandexLayer.addTo(self.map);
                    });
                  })
                  .catch((er) => {
                    throw new Error(er);
                  });
              } else {
                throw new Error('Out of limit');
              }
            });
        } else
        if (tileLayerOpt.layerType == 'OSM') {
          const url = tileLayerOpt.urlTemplate ||
            'https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png';
          const subdomains = tileLayerOpt.subdomains || "abc";
          const attribution = tileLayerOpt.attribution ||
            '&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors | &copy; <a href="http://gisw.ru">GISware Integro</a>';
          tileLayer(url, {
            subdomains,
            attribution
          }).addTo(this.map);
        } else {
          throw new Error('Unknown layer type: ' + tileLayerOpt.layerType);
        }
      } else {
        throw new Error('Layer type is not set');
      }
      this._mapClickLayer = new FeatureGroup();
      this._mapClickLayer.addTo(this.map);
    }
  }

  private _clearMapClickLayer() {
    if (this._mapClickLayer) {
      this._mapClickLayer.clearLayers();
    }
  }

  private _onSearchControlResult(data: IGeosearchResults): void {
    // console.log('_onSearchControlResult()', data);
    let result = data;
    // search for first result with latlng
    if (data.results.length) {
      for (let fry = 0; fry < data.results.length; fry++) {
        if (data.results[fry] && data.results[fry].latlng) {
          result = data.results[fry];
          break;
        }
      }
    }
    // show result on map
    this.fire('geocoder-result', {
      LAT: result.latlng.lat,
      LNG: result.latlng.lng,
      Deviation: 0,
      Address: data.name,
      Provider: data.provider.options.label
    });
    this._placeMarker(result);
    if (this.map) {
      this.map.setView(result.latlng, this._defaultMarkerZoomLevel);
    }
  }

  private _addEventsListener() {
    const geocoder = this.searchControl;
    if (geocoder) {
      geocoder.on('results', this._onSearchControlResult, this);
      geocoder.on('suggest', (e: any) => {
        this.fire('geocoder-suggest', {
          Provider: e.provider.options.label,
          Text: e.name || e.text,
          Suggestions: e.suggestions,
        });
      });
      geocoder.on('reverse', (e: any) => {
        var result = e.results && e.results[0];
        if (result) {
          const distance = e.coord.distanceTo(result.latlng);
          this.fire('geocoder-reverse-results', {
            LAT: e.coord.lat,
            LNG: e.coord.lng,
            Deviation: Math.round(distance),
            Address: result.name,
            Provider: e.provider.options.label
          })
        }
      });
      geocoder.on('results-error', (e: any) => {
        this.fire('geocoder-result-error', e)
      });
      geocoder.on('close', () => {
        this.fire('close');
        this.clean();
      });
    }
  }

  private _placeMarker(e: { latlng: LatLng }) {
    this._clearMapClickLayer();
    if (this._mapClickLayer) {
      const circle = new CircleMarker(e.latlng, {
        color: 'white',
        fillColor: '#f03',
        stroke: true,
        fillOpacity: 1,
        radius: 5
      });
      circle.addTo(this._mapClickLayer);
    }
  }

  private _onMapClick(e: LeafletEvent) {
    const ev = e as LeafletMouseEvent;
    const searchControl = this.searchControl;
    this._placeMarker(ev);
    if (searchControl) {
      // const providers = this.searchControl.options.providers;
      searchControl._reverse(ev.latlng).then((e: any) => {
        if (e && e.results && e.results.length) {
          const firstResult = e.results[0];
          const firstResultText = firstResult.name;
          searchControl.setInputText(firstResultText);
          const result = e.results && e.results[0];
          if (result) {
            const distance = e.coord.distanceTo(result.latlng);
            this.fire('geocoder-reverse', {
              LAT: e.coord.lat,
              LNG: e.coord.lng,
              Deviation: Math.round(distance),
              Address: e.results && e.results[0].name,
              Provider: e.provider.options.label
            });
          }
        }
      });
    }
  }

  private _addMapListeners() {
    if (this.map) {
      this.map.on('click', this._onMapClick, this);
    }
  }

  private _removeMapListeners() {
    if (this.map) {
      this.map.off('click', this._onMapClick, this);
    }
  }

}
