import { LatLng, LatLngBounds } from 'leaflet';
import { Klass } from '../../../../../common/Klass';
import { IGeocodeResult, IGeocoderProviderOptions } from './CustomProvider';

export type geocoderServiceSuggestFunc = (
  query: string,
  bounds: LatLngBounds,
  cb: (results: IGeocodeResult[]) => any,
  context: any,
  options?: any,
) => any;

export interface ICustomGeocoderService {
  options?: any;
  geocode?: (
    query: string,
    bounds: LatLngBounds,
    cb: (results: IGeocodeResult) => any,
    context: any,
    magicKey?: any,
    options?: any,
  ) => void;
  suggest?: geocoderServiceSuggestFunc;
  reverse?: (
    location: LatLng,
    scale: number,
    cb: (results: IGeocodeResult[]) => void,
    context: any,
    options?: any,
  ) => void;
  // [x: string]: any;
}

export type IGeocoderServiceConstructor = new(options: IGeocoderProviderOptions) => ICustomGeocoderService;

export class CustomGeocoder<O = any> extends Klass<O> implements ICustomGeocoderService {
  constructor(options: O) {
    super();
    this.options = Object.assign({}, this.options, options);
  }
}
