import L from 'leaflet';
import { Klass } from './Klass';

const Util = L.Util;

interface Event {
    type?: string;
    target?: any;
    [x: string]: any;
}

export type EventHandlerFn = (event: Event) => void;

/**
 * Leaflet base Evented class.
 * TODO: rewrite for work with Node Events https://nodejs.org/api/events.html
 *
 * A set of methods shared between event-powered classes (like Map and Marker).
 * Generally, events allow you to execute some function when something happens
 * with an object (e.g. the user clicks on the map, causing the map to fire
 * 'click' event).
 */
export class Evented<O extends {} = any> extends Klass<O> {

    private _events: {[type: string]: Array<{fn: EventHandlerFn, ctx: any}>};
    private _firingCount: number;
    private _eventParents: {[objId: number]: Evented};

    /**
     * Adds a listener function (fn) to a particular event type of the object.
     * You can optionally specify the context of the listener (object the this
     * keyword will point to). You can also pass several space-separated types
     * (e.g. 'click dblclick').
     */
    on(type: string, fn: EventHandlerFn, context?: any): this {
        // types can be a string of space-separated words
        const types = Util.splitWords(type);

        for (let i = 0, len = types.length; i < len; i++) {
            this._on(types[i], fn, context);
        }
        return this;
    }

    /**
     * Removes a previously added listener function. If no function is specified,
     * it will remove all the listeners of that particular event from the object.
     * Note that if you passed a custom context to on, you must pass the same context
     * to off in order to remove the listener.
     */
    off(type?: string, fn?: EventHandlerFn, context?: any): this {
        if (!type) {
            // clear all listeners if called without arguments
            delete this._events;
        } else {
            const types = Util.splitWords(type);

            for (let i = 0, len = types.length; i < len; i++) {
                this._off(types[i], fn, context);
            }
        }
        return this;
    }

    /**
     * Fires an event of the specified type. You can optionally provide a data
     * object — the first argument of the listener function will contain its properties.
     * The event might can optionally be propagated to event parents.
     */
    fire(type: string, data?: any, propagate?: boolean): this {
        if (!this.listens(type, propagate)) {return this; }

        const event = Object.assign({}, data, {
            type,
            target: this,
            sourceTarget: data && data.sourceTarget || this,
        });

        if (this._events) {
            const listeners = this._events[type];

            if (listeners) {
                this._firingCount = (this._firingCount + 1) || 1;
                for (let i = 0, len = listeners.length; i < len; i++) {
                    const l = listeners[i];
                    l.fn.call(l.ctx || this, event);
                }

                this._firingCount--;
            }
        }

        if (propagate) {
            // propagate the event to parents (set with addEventParent)
            this._propagateEvent(event);
        }

        return this;
    }

    /**
     * Returns true if a particular event type has any listeners attached to it.
     */
    listens(type: string, propagate?: boolean): boolean {
        const listeners = this._events && this._events[type];
        if (listeners && listeners.length) {return true; }

        if (propagate) {
            // also check parents for listeners if event propagates
            for (const id in this._eventParents) {
                if (this._eventParents[id].listens(type, propagate)) {return true; }
            }
        }
        return false;
    }

    /**
     * Behaves as on(...), except the listener will only get fired once and then removed.
     */
    once(type: string, fn: EventHandlerFn, context?: any): this {

        const handler = Util.bind(function () {
            this
                .off(type, fn, context)
                .off(type, handler, context);
        }, this);

        // add a listener that's executed once and removed after that
        return this
            .on(type, fn, context)
            .on(type, handler, context);
    }

    /**
     * Adds an event parent - an Evented that will receive propagated events
     */
    addEventParent(obj: Evented): this {
        this._eventParents = this._eventParents || {};
        this._eventParents[Util.stamp(obj)] = obj;
        return this;
    }

    /**
     * Removes an event parent, so it will stop receiving propagated events
     */
    removeEventParent(obj: Evented): this {
        if (this._eventParents) {
            delete this._eventParents[Util.stamp(obj)];
        }
        return this;
    }

    private _on(type: string, fn: EventHandlerFn, context?: any) {
        this._events = this._events || {};

        /* get/init listeners for type */
        let typeListeners = this._events[type];
        if (!typeListeners) {
            typeListeners = [];
            this._events[type] = typeListeners;
        }

        if (context === this) {
            // Less memory footprint.
            context = undefined;
        }
        const newListener = { fn, ctx: context };
        const listeners = typeListeners;

        // check if fn already there
        for (let i = 0, len = listeners.length; i < len; i++) {
            if (listeners[i].fn === fn && listeners[i].ctx === context) {
                return;
            }
        }

        listeners.push(newListener);
    }

    private _off(type: string, fn: EventHandlerFn, context?: any) {
        let listeners;
        let i;
        let len;

        if (!this._events) {return; }

        listeners = this._events[type];

        if (!listeners) {
            return;
        }

        if (!fn) {
            // Set all removed listeners to noop so they are not called if remove happens in fire
            for (i = 0, len = listeners.length; i < len; i++) {
                listeners[i].fn = Util.falseFn;
            }
            // clear all listeners for a type if function isn't specified
            delete this._events[type];
            return;
        }

        if (context === this) {
            context = undefined;
        }

        if (listeners) {

            // find fn and remove it
            for (i = 0, len = listeners.length; i < len; i++) {
                const l = listeners[i];
                if (l.ctx !== context) {continue; }
                if (l.fn === fn) {

                    // set the removed listener to noop so that's not called if remove happens in fire
                    l.fn = Util.falseFn;

                    if (this._firingCount) {
                        /* copy array in case events are being fired */
                        this._events[type] = listeners = listeners.slice();
                    }
                    listeners.splice(i, 1);

                    return;
                }
            }
        }
    }

    private _propagateEvent(e) {
        for (const id in this._eventParents) {
            if (!this._eventParents.hasOwnProperty(id)) {
                continue;
            }
            this._eventParents[id].fire(e.type, Object.assign({
                layer: e.target,
                propagatedFrom: e.target,
            }, e), true);
        }
    }
}

